import React, { Component } from 'react';
import validateLogin from "../../helpers/validateLogin";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import classnames from "classnames";
import { userLoginRequest } from "../../actions/loginActions";
import "./Login.css";
class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: "",
            password: "",
            errors: {},
            isLoading: false,
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        this.setState({
            [e.target.name]: e.target.value,
        });
    }
    isValid(data) {
        const { errors, isValid } = validateLogin(data);
        if (!isValid) {
            this.setState({ errors })
        }
        return isValid;
    }
    onSubmit(e) {
        e.preventDefault();
        if (this.isValid(this.state)) {
            this.setState({ errors: {}, isLoading: true });
            this.props.userLoginRequest({ username: this.state.username, password: this.state.password, call: "signin" }).then(
                (data) => { this.props.history.push("/command-center"); console.log("Data after succeful response login", data) },
                (data) => { this.setState({ errors: data.response.data, isLoading: false }); console.log("data after error login", data); console.log("stategkkfhjllllllllll", this.state); }
            );
        }
    }
    render() {
        const { username, password, isLoading, errors } = this.state;
        return (
            <div>
                <div className="back">
                    <div className="col col-md-4 offset-md-4 div-center" style={{ marginTop: 70 }}>
                        <div className="loginCenter">
                            <h3>Login</h3>
                            <hr />
                            <form onSubmit={this.onSubmit}>
                                {errors.form && <div className="alert alert-danger">{errors.form}</div>}
                                <div className={classnames("form-group", { "has-error": errors.username })}>
                                    {errors.username && <span className="help-block">{errors.username}</span>}
                                    {!errors.username && <label >Username</label>}
                                    <input type="username" class="form-control" name="username" value={this.state.username} placeholder="username" onChange={this.onChange} />
                                </div>
                                <div className={classnames("form-group", { "has-error": errors.password })}>
                                    {errors.password && <span className="help-block">{errors.password}</span>}
                                    {!errors.password && <label >Password</label>}
                                    <input type="password" class="form-control" name="password" onChange={this.onChange} placeholder="Password" />
                                </div>
                                <button style={{ backgroundColor: '#009dd6' }} type="submit" disabled={this.state.isLoading} class="btn ">Login</button>
                                <hr />
                                <a href="/signup" class="btn btn-link">Signup</a>
                                <button disabled={isLoading} type="button" class="btn btn-link">Reset Password</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        userLoginRequest: bindActionCreators(userLoginRequest, dispatch),
    }
}

export default connect(null, mapDispatchToProps)(Login);
