import React, { Component } from 'react'
import axios from 'axios';
import { Helmet } from "react-helmet";
class PerspectiveDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Perspective: null,
            Concept: null,
            Author: null,
            imgURL: null,
        }
    }
    async componentDidMount() {
        const { id } = this.props.match.params;
        let data = await axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/detail/${id}`)
        if (data.data.responseType == "success") {
            console.log(data);
            this.setState({
                Perspective: data.data.data,
                Concept: data.data.data.Concept,
                Author: data.data.data.Author,
                imgURL: data.data.img
            })
        }
    }
    render() {
        return (
            <div className="text-center" style={{ marginTop: '50px' }}>
                {this.state.Perspective && this.state.Concept && this.state.Author &&
                    <React.Fragment>
                        <Helmet>
                           
                            <meta property="og:url" content={`http://conceptionary.io/perspective/${this.state.Perspective.id}`} />
                            <meta property="og:type" content="website" />
                            <meta property="fb:app_id" content="930857800581355" />
                            <meta property="og:title" content={this.state.Concept.name.charAt(0).toUpperCase() + this.state.Concept.name.slice(1)} />
                            <meta property="og:description" content={this.state.Perspective.description} />
                            <meta property="og:image" content={this.state.imgURL} />
                        
                           

                            <meta name="twitter:card" content="summary_large_image" />
                            <meta name="Twitter:site" content="Conceptionary" />
                            <meta name="twitter:creator" content="Conceptionary.com" />
                            <meta name="twitter:title" content={this.state.Concept.name.charAt(0).toUpperCase() + this.state.Concept.name.slice(1)} />
                            <meta name="twitter:description" content={this.state.Perspective.description} />
                            <meta name="twitter:image" content={this.state.imgURL} />
                                                    
                        </Helmet>
                        <div>
                            <img src={this.state.imgURL} style={{ width: '200px', height: '200px' }} alt={this.state.Author.lastName}></img>
                            <h3>{this.state.Concept.name}</h3>
                            <hr></hr>
                            <p><b>{this.state.Concept.name}</b> {this.state.Perspective.description}</p>
                            <h5>{this.state.Author.firstName + " " + this.state.Author.lastName}</h5>
                        </div>
                    </React.Fragment>
                }
                {/* <Helmet>
                    <meta property="og:url" content={`http://conceptionary-frontend.s3-website.us-east-2.amazonaws.com/perspective/${this.state.Perspective.id}`} />
                    <meta property="og:type" content="website" />
                    <meta property="fb:app_id" content="930857800581355" />
                    <meta property="og:title" content={this.state.Concept.name} />
                    <meta property="og:description" content={this.state.Perspective.description} />
                    <meta property="og:image" content="https://www.recordedbooks.com/RBDigital/media/site/new%20stuff/Authors%20600%20X%20600/Authors_Stephen-King_600x600_1.jpg?ext=.jpg" />
                </Helmet>
                <div>
                    <img alt="" src="https://www.recordedbooks.com/RBDigital/media/site/new%20stuff/Authors%20600%20X%20600/Authors_Stephen-King_600x600_1.jpg?ext=.jpg" style={{ width: '200px', height: '200px' }}></img>
                    <h3>{this.state.Concept.name}</h3>
                    <hr></hr>
                    <p><b>{this.state.Concept.name}</b> {this.state.Perspective.description}</p>
                    <h5>{this.state.Author.firstName + " " + this.state.Author.lastName}</h5>
                    <p>conceptionary site</p>
                </div> */}
            </div>
        )
    }
}

export default PerspectiveDetail