
const initialState = {
    // searchSuggestions: [],
    tags: [],
    selectedOption: "",
    authorClusterSelectedOptions: "",
    searchCategory: "All",
    concepts: [],
    authors: [],
    conceptClusters: [],
    authorClusters: [],
    authorGroups: [],
    loading: true,
    userExist_admin: false,
    loginStatus_admin: false,
    loggedInUserAccount_admin: {},
    loginRequestStatus_admin: true,
    invalidLogin: false,
    SIGNUP_SUCCESS: false,
}
export default (state = initialState, action = {}) => {
    switch (action.type) {
        case "GET_CONCEPTS_NAMES_NAVBAR":
            // console.log("GET_CONCEPTS_NAMES_NAVBAR");
            return {
                ...state,
                searchSuggestions: action.payload,
            }
        case "SET_SEARCH_CATEGORY":
            // console.log("SET_SEARCH_CATEGORY ");
            return {
                ...state,
                searchCategory: action.payload,
            }
        case "SET_SELECTED_OPTION":
            // console.log("SET_SELECTED_OPTION");
            return {
                ...state,
                selectedOption: action.payload
            }
        case "FLUSH_NAV_BAR":
            return {
                ...state,
                selectedOption: "",
            }
        case "SET_AUTHOR_CLUSTER_SELECTED_OPTIONS":
            return {
                ...state,
                authorClusterSelectedOptions: action.payload,
            }
        case "CONCEPTS_TRIMMED_FOR_NAVBAR":
            // console.log(action.payload);
            // console.log("CONCEPT_CLUSTERS_TRIMMED_FOR_NAVBAR")
            return {
                ...state,
                concepts: action.payload,
            }
        case "CONCEPT_CLUSTERS_TRIMMED_FOR_NAVBAR":
            // console.log("CONCEPT_CLUSTERS_TRIMMED_FOR_NAVBAR")
            return {
                ...state,
                conceptClusters: action.payload,
            }
        case "AUTHORS_TRIMMED_FOR_NAVBAR":
            // console.log("AUTHORS_TRIMMED_FOR_NAVBAR")
            return {
                ...state,
                authors: action.payload,
            }
        case "AUTHOR_CLUSTERS_TRIMMED_FOR_NAVBAR":
            // console.log("AUTHOR_CLUSTERS_TRIMMED_FOR_NAVBAR")
            return {
                ...state,
                authorClusters: action.payload,
            }

        case "AUTHOR_GROUPS_TRIMMED_FOR_NAVBAR":
            return {
                ...state,
                authorGroups: action.payload,
            }

        case "LOGIN_REQUEST":
            return {
                ...state,
                loginRequestStatus_admin: false,
                invalidLogin: false,
                loginStatus_admin: false

            }

        case "LOGIN_SUCCESS":
            // return state;
            return {
                ...state,
                userExist_admin: true,
                loggedInUserAccount_admin: action.userInfo,
                loginRequestStatus_admin: true,
                loginStatus_admin: true,
                invalidLogin: false

            }

        case "LOGIN_FAILURE":
            return {
                ...state,
                loginRequestStatus_admin: true,
                loginStatus_admin: false,
                invalidLogin: true
            }

        case "LOGOUT":
            return {
                ...state,
                loginStatus_admin: false,
                loggedInUserAccount_admin: {},
                loginRequestStatus_admin: false,
                userExist_admin: false,
            }
        case "SIGNUP_SUCCESS":
            return {
                ...state,
                SIGNUP_SUCCESS: true
            }
        case "ADD_TAG": {
            let tags = [...state.tags]
            if(Array.isArray(action.payload)){
                tags.push(...action.payload)
            }else{
                tags.push(action.payload)
            }
            return {
                ...state,
                tags: tags
            }
        }
        case "REMOVE_TAG":
            let tags = [...state.tags];
            for (let i = 0; i < tags.length; i++) {
                if (tags[i].id == action.payload) {
                    tags.splice(i, 1);
                }
            }
            return {
                ...state,
                tags: tags
            }


        default:
            return state;
    }
}