import { stat } from "fs";

const initialState = {
    authorsWithDetails: {},
    authorBioHeadings: {},
    authorBooks: {},
    authorPerspectives:{},
    authorGroups:{},
    concepts:{},
    author:{},
    apiLoading: false,
    apiServerError: false,
    apiNetworkError: false,
    author_authors_apiLoading: false,
    author_authors_apiServerError: false,
    author_authors_apiNetworkError: false,
    author_authors: {
    AuthorInfluenceAuthors: [],
    AuthorConvoAuthors: [],
    }
}
export default (state = initialState, action = {}) => {
    switch (action.type) {
        case 'AUTHOR_DETAILS':{
            let author=action.payload.authors[Object.keys(action.payload.authors)[0]]
            return{
                ...state,
                authorPerspectives:action.payload.perspectives,
                authorBioHeadings:action.payload.AuthorBioHeadings,
                authorGroups:action.payload.AuthorGroups,
                author:author,
                concepts:action.payload.concepts,
                authorBooks:action.payload.books
            }
        }
        case 'AUTHOR_PERSPECTIVE_LIKE':{
            let perspectives = state.authorPerspectives;
            perspectives[action.payload.id].loves = action.payload.loves;
            perspectives[action.payload.id].isLike = action.payload.isLike;
            // return{
            //     ...state,
            //     authorPerspectives: perspectives
            // }
        }
        case 'SERVER_ERROR':
            return {
                ...state,
                apiServerError: true,
            }
        case 'NETWORK_ERROR':
            return {
                ...state,
                apiNetworkError: true,
            }
        case 'REQUEST_STARTED':
            return {
                ...state,
                apiLoading: true,
            }
        case "GET_AUTHOR_DETAILS":
            return {
                ...state,
                authorsWithDetails: action.payload,
                apiLoading: false,
                apiServerError: false,
                apiNetworkError: false,
            }
        case "GET_AUTHOR_AUTHORS_PENDING":
            return {
                ...state,
                author_authors_apiLoading: true,
            }
        case 'GET_AUTHOR_AUTHORS_SUCCESS':
            // console.log('reducer get author success')
            return {
                ...state,
                author_authors_apiLoading: false,
                author_authors_apiServerError: false,
                author_authors_apiNetworkError: false,
                author_authors: action.payload
            }
        case 'GET_AUTHOR_AUTHORS_SERVER_ERROR':
            return {
                ...state,
                author_authors_apiServerError: true,
                author_authors_apiLoading: false,
            }
        case 'GET_AUTHOR_AUTHORS_NETWORK_ERROR':
            return {
                ...state,
                author_authors_apiNetworkrError: true,
                author_authors_apiLoading: false,
            }
        case "GET_AUTHOR_BIO":
            // console.log("Get AuthorBio dispatch called in reducer catching");
            return {
                ...state,
                authorBioData: [...action.payload]
            }
        case "GET_AUTHOR_BOOKS":
            // console.log("Get AuthorBooks dispatch called in reducer catching");
            return {
                ...state,
                authorBooks: [...action.payload]
            }
        case 'GET_AUTHOR_INFLUENCED_BY_AUTHORS':
            return {
                ...state,
                authorInfluencedByAuthors: [...action.payload]
            }
        case 'GET_AUTHOR_INFLUENCED_ON_AUTHORS':
            return {
                ...state,
                authorInfluencedOnAuthors: [...action.payload]
            }
        case 'GET_AUTHOR_CONVO_WITH_AUTHORS':
            return {
                ...state,
                authorConvoWithAuthors: [...action.payload]
            }
        case 'FLUSH_AUTHOR':
            return {
                ...state,
                loading: true,
            }
        case 'LOADING_FALSE':
            return {
                ...state,
                loading: false,
            }
        default:
            return state;
    }
}