import React, { Component } from 'react';
import './App.css';
import { Router, Route, Link, Switch, Redirect } from 'react-router-dom';
import ConceptsPage from "./components/ConceptsPage/ConceptsPage";
import ConceptClustersPage from "./components/ConceptClustersPage/ConceptClustersPage";
import Message from "./components/Messages/Message";
import AuthorCluster from "./components/AutherCluster/AutherCluster";
import AuthorPage from "./components/AuthorPage/AutherPage";
import Login from "./components/Login/Login";
import Signup from "./components/Signup/Signup";
import TopNavBar from "./components/TopNavBar/TopNavBar";
import HomePage from "./components/HomePage/HomePage";
import FileUpload from './components/FileUpload/FileUpload';
import PerspectiveDetail from './components/perspectiveDetail/perspective';
import { connect } from 'react-redux';
import { createBrowserHistory } from "history";
const history = createBrowserHistory()
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isAuthenticated: false,
      user: this.props.auth.user,
    }
  }
  render() {
    const { isAuthenticated } = this.props.auth;
    // const { id, username, email } = this.props.auth.user;
    const PrivateRoute = ({ component: Component, ...rest }) => {
      // console.log("...Rest", rest);
      return <Route {...rest} render={(props) => {
        // console.log("Props in Private Route", props);
        return isAuthenticated === true ? <Component {...props} /> : <Redirect to="/login" />
      }} />
    };
    return (
       <Router history={history}>
        <div>
          <TopNavBar />
          <Switch>
            <Route path="/" exact component={HomePage} />
            <Route path="/login" exact component={Login} />
            <Route path="/signup" exact component={Signup} />
            <Route path="/concepts" exact component={ConceptsPage} />
            <Route path="/concepts/:name" exact component={ConceptsPage} />
            <Route path="/concept-clusters" exact component={ConceptClustersPage} />
            <Route path="/concept-clusters/:name" exact component={ConceptClustersPage} />
            <Route path="/authors" exact component={AuthorPage} />
            <Route path="/authors/:name" exact component={AuthorPage} />
            <Route path="/author-clusters" exact component={AuthorCluster} />
            <Route path="/author-groups" exact component={AuthorCluster} />
            <Route path="/author-groups/:name" exact component={AuthorCluster} />
            <Route path="/author-clusters/:name" exact component={AuthorCluster} />
            <Route path="/Message" exact component={Message} />
            <Route path="/Fileupload" exact component={FileUpload}/>
            <Route path="/perspective/:id" exact component={PerspectiveDetail}/>
          </Switch>
        </div>
        </Router>
    );
  }
}
function mapStatToProps(state) {
  return {
    auth: state.auth,
  }
}
export default connect(mapStatToProps)(App);
