import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js';
import './style.css';
import Axios from 'axios';
class FileUpload extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedFile: null,
            msg: "",
            skip: [],
            wait:false
        }
        this.onChangeHandler = this.onChangeHandler.bind(this);
        this.onClickHandler = this.onClickHandler.bind(this);
    }

    onChangeHandler(event) {
        this.setState({
            selectedFile: event.target.files[0],
        });
    }
    async onClickHandler() {
        this.setState({
            wait:true
        })
        const data = new FormData();
        data.append('file', this.state.selectedFile);
        let response = await Axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspectives/upload`, data);
        if (response.data.msg) {
            this.setState({
                msg: response.data.msg,
                wait:false
            })
        }
        if (response.data.skip.length > 0) {
            this.setState({
                skip: response.data.skip
            })
        }
    }
    render() {
        return (
            <div class="container" style={{ marginTop: "5px" }}>
                <div class="row">
                    <div class="offset-md-3 col-md-6">
                        <div class="form-group files">
                            <label><b>Upload Your File </b></label>
                            <input type="file" name="file" class="form-control" multiple onChange={this.onChangeHandler} /><br></br>
                        </div>
                        <div class="form-group">
                            {/*<ToastContainer />
                <Progress max="100" color="success" value={this.state.loaded} >{Math.round(this.state.loaded,2) }%</Progress>*/}

                        </div>
                        <button type="button" class="btn btn-success btn-block" onClick={this.onClickHandler} disabled={(this.state.selectedFile?false:true)||(this.state.wait?true:false)}>Upload</button>
                    </div>
                </div>
                <div class="row" style={{ display: this.state.msg ? 'block' : 'none' }}>
                    <div class="offset-md-3 col-md-6" style={{ marginTop: '50px' }}>
                        <div class="alert alert-info alert-dismissible fade show" role="alert">
                            <strong>{this.state.msg}</strong>
                            <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                    </div>
                </div>
                <div class="row" style={{ marginTop: '50px' }}>
                    <div class="class=offset-md-3 col-md-6">
                        {
                            this.state.wait&&(
                                <h2>please wait......</h2>
                            )
                        }
                    </div>
                </div>
                <div class="row" style={{ marginTop: '50px' }}>
                    <div class="class=offset-md-2 col-md-6">
                        <ul style={{listStyleType:'none'}}>
                           {
                               this.state.skip.length>0&&(
                                   this.state.skip.map(i=>{
                                       return <li>row:{i} not saved from file</li>
                                   })
                               )
                           }
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}

export default FileUpload

