
import {normalize} from 'normalizr';
import {conceptClusterSchema} from '../schema/conceptClusterSchema';
const axios = require('axios');
export function flushConceptClusterState() {
    return dispatch => {
        dispatch({
            type: "FLUSH_CONCEPT_CLUSTER",
        })
    }
}
export function increasePerspectiveLike(id) {
    let like = {};
    like.type = "increase";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = true;
                dispatch({
                    type: 'CONCEPT_CLUSTER_PERSPECTIVE_LIKE',
                    payload: res.data.perspective
                })
            })
    }
}
export function decreasePerspectiveLike(id) {
    let like = {};
    like.type = "decrease";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = false;
                dispatch({
                    type: 'CONCEPT_CLUSTER_PERSPECTIVE_LIKE',
                    payload: res.data.perspective
                })
            })
    }
}
export function setClickedState(obj){
    return dispatch=>{
        dispatch({
            type:'CLICKED_STATE',
            payload:obj
        })
    }
}
export function getConceptClusterFromApi(id) {
    return dispatch => {
        dispatch({
            type: "REQUEST_STARTED"
        })
        axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/conceptClusters/${id}?relationships=true`).then(data => {
            const { responseType } = data.data;
            if (responseType == 'success') {
                const conceptDetailsFromApi = data.data.data;
                const normalizedData=normalize(conceptDetailsFromApi,conceptClusterSchema)
                dispatch({
                    type: "REQUEST_SUCCESSFUL",
                    payload: conceptDetailsFromApi,
                })
                dispatch({
                    type:'CONCEPT_CLUSTER_DETAIL',
                    payload:normalizedData.entities
                })
            }
            else {
                dispatch({
                    type: "SERVER_ERROR"
                })
            }
        }).catch(error => {
            dispatch({
                type: "NETWORK_ERROR"
            })
            console.log('Error While fetching concept details from api', error);
        })
    }
}