import Axios from "axios";
export function getAuthorGroups(filters){
    let filterobj = {};
    filterobj.filters = filters;
    return new Promise((resolve, reject) => {
        Axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/authorGroups`, filterobj)
            .then(data => {
                resolve(data.data);
            })
            .catch(err => {
                console.log(err);
            })
    });
}