import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './Context.css'
class Context extends Component {
    constructor(props) {
        super(props);
        this.state = {
            visibility: 'none',
        }
    }

    render() {
        let {authors,data}=this.props;
        return (
            <div id={data.ConceptId}>
                <div id={authors[data.Author].lastName} className="clearfix" style={{ marginTop: "10px", backgroundColor: "white", padding: "10px", border: "1px solid white", borderRadius: "5px" }} >
                    <b style={{  cursor: "pointer" }}
                        onClick={() => {
                            this.props.flushConceptClusterState();
                            this.props.setSelectedItem({ label: data.ConceptName, value: data.ConceptName, id: data.ConceptId, category: "Concepts" })
                        }}>{data.pronoun}{" "} {data.ConceptName}</b>{" is "}{data.description}
                    <div onClick={() => this.props.onAuthorClick({ auth_id: data.AuthorId, author_first: authors[data.Author].firstName, author_last: authors[data.Author].lastName, born: authors[data.Author].dob, died: authors[data.Author].dod })} style={{ color: "red", cursor: "pointer" }}>{authors[data.Author].firstName}{" "}{authors[data.Author].lastName}</div>
                    <div className="text-right">
                        <i style={{ color: data.isLike?"red":"grey",fontSize:20}} className="fa fa-thumbs-up  " onClick={()=>{this.props.onLikeClick(data.id)}}></i>&nbsp;[{data.loves?data.loves:0}]&nbsp;&nbsp; <span  id="ConceptEllipseCircle"  className="contextbutton  fa fa-ellipsis-h" onClick={() => {
                            this.setState({
                                visibility: this.state.visibility == "none" ? "block" : "none",
                            })
                        }} style={{ cursor: "pointer" }}></span>
                    </div>
                    <div id={data.id} style={{ marginTop: "20px",textAlign:'justify', display: [this.state.visibility] }} >{data.longDescription}<div style={{ backgroundColor: "#DCDCDC" }}>{data.citation}</div></div>
                </div>
            </div>);
    }
}
// Context.PropTypes = {
//     data: PropTypes.object.isRequired,
//     flushConceptClusterState: PropTypes.func.isRequired,
//     setSelectedItem: PropTypes.func.isRequired,
//     onAuthorClick: PropTypes.func.isRequired,
// }
export default Context;