
export default function getUniqueSortedAuthors(entities, ids) {
    let perspectiveids = entities.concepts[ids].Perspectives;
    let perspectivesRelations = entities.perspectivesRelations;
    let authors = entities.authors;
    if (perspectiveids && perspectiveids.length > 0 && authors && perspectivesRelations) {
        let uniqueAuthorWithDetails = [];
        let uniqueAuthorNames = [];
        perspectiveids.forEach(id => {
            if (!(uniqueAuthorNames.indexOf((authors[perspectivesRelations[id].Author].firstName + authors[perspectivesRelations[id].Author].lastName)) > -1)) {
                uniqueAuthorNames.push((authors[perspectivesRelations[id].Author].firstName + authors[perspectivesRelations[id].Author].lastName));
                uniqueAuthorWithDetails.push(authors[perspectivesRelations[id].Author]);
            }
        })
        return uniqueAuthorWithDetails.sort(function (a, b) {
            let authorALastName = a.lastName.toUpperCase();
            let authorBLastName = b.lastName.toUpperCase();
            return (authorALastName < authorBLastName) ? -1 : (authorALastName > authorBLastName) ? 1 : 0;
        });
    }
}