import React, { Component } from 'react';
import { NavLink } from "react-router-dom";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import conceptsSorter from "../../utils/conceptsSorterFunction";
import { Link } from "react-router-dom";
import ReactLoading from 'react-loading';
import Axios from 'axios';
import queryString from "query-string";
import { Redirect } from 'react-router-dom';
import { withRouter } from 'react-router-dom'
import { getAutherClusterDetailsViaSupra,
     getAutherClusterDetails,
      flushAuthorClusterState,
       getAuthorCluster,
       setClickedState,
       increasePerspectiveLike,
       decreasePerspectiveLike
    } from "../../actions/autherCluster";
import { setSelectedCategory, setSelectedItem, flushState, addTag } from "../../actions/navbaractions";
import {
    getUniqueSortedConceptNameFromAuthorCluster,
    getDynamicSortedAuthorClusterWithDetails,
    getSortedAuthorClusterWithDetailsByAuthorLast
} from '../../selectors';
class AutherCluster extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedOption: this.props.navbar.selectedOption,
            //authorClustersWithDetails: this.props.autherCluster.authorClustersWithDetails,
            // authorClusterConceptsDiscrete: this.props.autherCluster.authorClusterConceptsDiscrete,
            apiLoading: this.props.autherCluster.apiLoading,
            apiServerError: this.props.autherCluster.apiServerError,
            apiNetworkError: this.props.autherCluster.apiNetworkError,
            // authorClicked: false,
            // conceptClicked: true,
            //Normalized states
            sortedConceptsNames: this.props.sortedConceptsNames,
            dynamicSortedAuthorCluster: this.props.dynamicSortedAuthorCluster,
            sortedAuthorClusterDetail: this.props.sortedAuthorClusterDetail,
            perspectives:this.props.autherCluster.authorCluster,
            //End of normalized staes
        }
        this.onAuthorClick = this.onAuthorClick.bind(this);
        this.onLikeClick=this.onLikeClick.bind(this);
        //this.getDynamicSortedAuthorClusterWithDetails = this.getDynamicSortedAuthorClusterWithDetails.bind(this);
        if (this.props.navbar.selectedOption) {
            if (this.props.navbar.selectedOption.id) {
                if (this.props.navbar.selectedOption.category == "Author-Clusters") {
                    this.props.getAuthorCluster(this.props.navbar.selectedOption.id, "Author Clusters")
                    // if (this.props.navbar.selectedOption.via) {
                    //     // console.log("Helo");
                    //     this.props.getAutherClusterDetailsViaSupra(this.props.navbar.selectedOption.id);
                    // }
                    // this.props.getAutherClusterDetails(this.props.navbar.selectedOption.id);
                }
                if (this.props.navbar.selectedOption.category == "Author-Groups") {
                    this.props.getAuthorCluster(this.props.navbar.selectedOption.id, "Author Groups")
                    // if (this.props.navbar.selectedOption.via) {
                    //     // console.log("Helo");
                    //     this.props.getAutherClusterDetailsViaSupra(this.props.navbar.selectedOption.id);
                    // }
                    // this.props.getAutherClusterDetails(this.props.navbar.selectedOption.id);
                }
            }
        }
    }
    getAutherCluster(cluster) {
        return new Promise((resolve, reject) => {
            Axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/authorClusters`, { cluster }).then(data => {
                if (data.data.obj) {
                    let result = data.data.obj.selectedOption;
                    resolve(result);
                }

                resolve("");
            }).catch(err => {
                console.log(err);
            });
        });
    }
    getAuthorGroups = (filters) => {
        let filterobj = {};
        filterobj.filters = filters;
        return new Promise((resolve, reject) => {
            Axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/authorGroups`, filterobj)
                .then(data => {
                    resolve(data.data);
                })
                .catch(err => {
                    console.log(err);
                })
        });
    }
   async getAuthorGroupByName(name){
       let filters=[this.props.match.params.name];
       let authorGroup=await this.getAuthorGroups(filters);
       if(authorGroup[0]){
           authorGroup=authorGroup[0]
           this.props.setSelectedItem({
               label:authorGroup.label,
               value:authorGroup.value,
               id:authorGroup.id,
               category:'Author-Groups'
           });
       }
   }
    async componentWillMount() { 
        let url=this.props.location.pathname.split('/')[1];
        if (this.props.match.params.name) {
            if (this.props.match.params.name && queryString.parse(this.props.location.search).filters) {
                let filters = [];
                if(url=="author-groups"||url=="Author-Groups"){
                    this.getAuthorGroupByName();
                  }else{
                    let queryFilters = queryString.parse(this.props.location.search).filters;
                    if (typeof queryFilters === "string") {
                        filters.push(queryFilters);
                    } else {
                        filters = queryFilters;
                    }
                    let author_group = await this.getAuthorGroups(filters);
                    let cluster = {};
                    cluster.name = this.props.match.params.name;
                    cluster.groupIds = author_group.map(group => group.id);
                    let result = await this.getAutherCluster(cluster);
                    if (result) {
                        this.props.addTag(author_group);
                        this.props.setSelectedItem(result);
                        this.setState({
                            selectedOption: result
                        });
                    } else {
                        this.props.history.push("/Message");
                    }
                  }
            }
            else {
                if(url=="author-groups"||url=="Author-Groups"){
                  this.getAuthorGroupByName();
                }
                let cluster = {};
                cluster.name = this.props.match.params.name;
                cluster.groupIds = [];
                let result = await this.getAutherCluster(cluster);
                if (result) {
                    this.props.setSelectedItem(result);
                    this.setState({
                        selectedOption: result
                    });
                    this.props.getAuthorCluster(this.props.navbar.selectedOption.id, "Author Clusters");
                } else {
                    this.props.history.push("/Message");
                }

            }
        }
    }
    async componentWillReceiveProps(nextProps) {
        if (this.props.navbar.tags.length !== nextProps.navbar.tags.length) {
            if (nextProps.navbar.selectedOption && nextProps.navbar.selectedOption.category == "Author-Clusters") {
                let cluster = {};
                cluster.name = nextProps.navbar.selectedOption.value;
                cluster.groupIds = nextProps.navbar.tags.map(group => group.id);
                let result = await this.getAutherCluster(cluster);
                if (result) {
                    this.props.setSelectedItem(result);
                    this.setState({
                        selectedOption: result
                    });
                    this.props.getAuthorCluster(this.props.navbar.selectedOption.id, "Author Clusters");
                } else {
                    this.props.history.push("/Message");
                }

            }
        }

        if (this.props.navbar.tags.length !== nextProps.navbar.tags.length) {
            if (nextProps.navbar.selectedOption && nextProps.navbar.selectedOption.category == "Author-Groups") {
                let cluster = {};
                cluster.name = nextProps.navbar.selectedOption.value;
                cluster.groupIds = nextProps.navbar.tags.map(group => group.id);
                let result = await this.getAutherCluster(cluster);
                if (result) {
                    this.props.setSelectedItem(result);
                    this.setState({
                        selectedOption: result
                    });
                    this.props.getAuthorCluster(this.props.navbar.selectedOption.id, "Author Groups");
                } else {
                    this.props.history.push("/Message");
                }

            }
        }


        if (this.state.selectedOption != nextProps.navbar.selectedOption) {
            this.setState({
                selectedOption: nextProps.navbar.selectedOption,
                //authorClustersWithDetails: nextProps.autherCluster.authorClustersWithDetails,
                apiLoading: nextProps.autherCluster.apiLoading,
                apiServerError: nextProps.autherCluster.apiServerError,
                apiNetworkError: nextProps.autherCluster.apiNetworkError,
                // authorClusterConceptsDiscrete: nextProps.autherCluster.authorClusterConceptsDiscrete,
                //Normalized states
                sortedConceptsNames: nextProps.sortedConceptsNames,
                dynamicSortedAuthorCluster: nextProps.dynamicSortedAuthorCluster,
                sortedAuthorClusterDetail: nextProps.sortedAuthorClusterDetail,
                perspectives:nextProps.autherCluster.authorCluster,
                //End of normalized staes
            })
            if (nextProps.navbar.selectedOption.id && nextProps.navbar.selectedOption.category == "Author-Clusters") {
                this.props.getAuthorCluster(nextProps.navbar.selectedOption.id, "Author Clusters")
            }

            if (nextProps.navbar.selectedOption.id && nextProps.navbar.selectedOption.category == "Author-Groups") {
                this.props.getAuthorCluster(nextProps.navbar.selectedOption.id, "Author Groups")
            }
        }
        else {
            this.setState({
                //authorClustersWithDetails: nextProps.autherCluster.authorClustersWithDetails,
                apiLoading: nextProps.autherCluster.apiLoading,
                apiServerError: nextProps.autherCluster.apiServerError,
                apiNetworkError: nextProps.autherCluster.apiNetworkError,
                //authorClusterConceptsDiscrete: nextProps.autherCluster.authorClusterConceptsDiscrete,
                //Normalized states
                sortedConceptsNames: nextProps.sortedConceptsNames,
                dynamicSortedAuthorCluster: nextProps.dynamicSortedAuthorCluster,
                sortedAuthorClusterDetail: nextProps.sortedAuthorClusterDetail,
                perspectives:nextProps.autherCluster.authorCluster
                //End of normalized staes
            })
        }
        // console.log("author clusters State", this.state);
    }
    onAuthorClick(author) {
        let label = author.author_first + " " + author.author_last;
        let value = label;
        let id = author.auth_id;
        let born = author.born;
        let died = author.died;
        let category = "Authors"
        this.props.flushAuthorClusterState()
        this.props.setSelectedItem({ label, value, id, born, died, category });
    }
    onLikeClick(id) {
        if (this.state.perspectives[id].isLike) {
            this.props.decreasePerspectiveLike(id);
        }
        else {
            this.props.increasePerspectiveLike(id);
        }
    }
    // getSortedAuthorClusterWithDetailsByAuthorLast(AuthorClusterWithDetails) {
    //     let copyAuthorClusterWithDetails = [...AuthorClusterWithDetails];
    //     copyAuthorClusterWithDetails.sort(function (a, b) {
    //         var textA = a.authorLastName.toUpperCase();
    //         var textB = b.authorLastName.toUpperCase();
    //         return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
    //     });
    //     return copyAuthorClusterWithDetails;
    // }
    // getDynamicSortedAuthorClusterWithDetails(AuthorClusterWithDetails) {
    //     let copyAuthorClusterWithDetails = [...AuthorClusterWithDetails];
    //     if (this.state.authorClicked) {
    //         copyAuthorClusterWithDetails.sort(function (a, b) {
    //             var textA = a.authorLastName.toUpperCase();
    //             var textB = b.authorLastName.toUpperCase();
    //             return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
    //         });
    //     }
    //     else if (this.state.conceptClicked) {
    //         copyAuthorClusterWithDetails.sort(function (a, b) {
    //             var authorA = a.authorLastName.toUpperCase();
    //             var authorB = b.authorLastName.toUpperCase();
    //             var textA = a.conceptName.toUpperCase();
    //             var textB = b.conceptName.toUpperCase();
    //             if (textA === textB) {
    //                 return (authorA < authorB) ? -1 : (authorA > authorB) ? 1 : 0;
    //             }
    //             else {
    //                 if (textA === "CULTURE") {
    //                     console.log("not author sorting");
    //                 }
    //                 return (textA < textB) ? -1 : 1;
    //             }
    //             // return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
    //         });
    //     }
    //     return copyAuthorClusterWithDetails;
    // }
    render() {
        let arr = [];
        return (
            <div className="overflowhidden" style={{ overflow: "hidden" }}>
                {this.state.selectedOption.id && (this.state.selectedOption.category == "Author-Clusters" || this.state.selectedOption.category == "Author-Groups") && <div className="container-fluid">
                    {this.state.apiLoading && <div style={{ width: "100px", margin: "auto", marginTop: "30vh" }}>
                        <ReactLoading type={"balls"} color={"red"} />
                    </div>}
                    {!this.state.apiLoading && !this.state.apiNetworkError && !this.state.apiServerError && <div className="row">
                        <div className="col-xs-12 col-sm-12 col-md-3" style={{ borderRadius: "3px", backgroundColor: "#F6F0EF", height: "89vh", overflow: "auto", textAlign: "" }}>
                            <div class="mb-2 bg-white text-center p-2">
                                <b><h4>{ this.state.selectedOption.label}</h4></b>
                            </div>
                            <div class="bg-white">
                            {this.state.sortedConceptsNames && (this.state.sortedConceptsNames.length > 0 && <div style={{ width: "150px", margin: "auto" }}>
                                {this.state.sortedConceptsNames.map(concept => {
                                    if (concept.name != undefined) {
                                        return <a onClick={() => {
                                            this.props.setClickedState(
                                                {
                                                    authorClicked: false,
                                                    conceptClicked: true,
                                                }
                                            )

                                        }} href={"#" + concept.id} style={{ cursor: "pointer", textDecoration: "none", color: "black", display: "block" }} ><b>{concept.name}</b></a>
                                    }
                                })}
                            </div>)}</div>
                        </div>
                        <div className="col-xs-12 col-sm-12 col-md-4" style={{ borderRadius: "3px", backgroundColor: "#F6F0EF", height: "89vh", overflow: "auto" }}>
                            <div id="main-content" style={{ backgroundColor: "#F6F0EF" }}>
                                <div id="img" style={{ width: "398px", height: "280px", backgroundColor: "red" }}>
                                    <h4 style={{ zIndex: "3", padding: "10px", color: "white" }} className="pull-right">{this.state.selectedOption ? this.state.selectedOption.label : ""}</h4>
                                </div>
                                {/* <div style={{ marginTop: "10px", border: "1px solid white", paddingTop: "5px", borderRadius: "5px", width: "398px", textAlign: "center", backgroundColor: "white" }}>
                                    <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                        <input type="checkbox" value="" />Positive
                                    </label>
                                    <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                        <input type="checkbox" value="" />Critical
                                    </label>
                                    <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                        <input type="checkbox" value="" />Funny
                                    </label>
                                </div> */}
                                {
                                    this.state.dynamicSortedAuthorCluster && this.state.dynamicSortedAuthorCluster.length > 0 && this.state.dynamicSortedAuthorCluster.map(data => {
                                        return (<div id={data.concept_id}><div id={data.authorLastName} className="clearfix" style={{ marginTop: "10px", backgroundColor: "white", padding: "10px", border: "1px solid white", borderRadius: "5px" }} >
                                            <b style={{ color: "blue", cursor: "pointer" }} onClick={() => {
                                                this.props.flushAuthorClusterState();
                                                this.props.setSelectedItem({ label: data.conceptName, value: data.conceptName, id: data.conceptId, category: "Concepts" })
                                            }}>{data.pronoun}{" "} {data.conceptName}</b>{" is "}{data.description}
                                            <div onClick={() => this.onAuthorClick({ auth_id: data.authorId, author_first: data.authorFirstName, author_last: data.authorLastName, born: "1885", died: "1960" })} style={{ color: "red", cursor: "pointer" }}>{data.authorFirstName}{" "}{data.authorLastName}</div>
                                            <div className="pull-right">
                                                <i style={{ color:data.isLike?"red":"grey",fontSize:20}} className="fa fa-thumbs-up" onClick={()=>{this.onLikeClick(data.id)}}></i>&nbsp;[{data.loves ? data.loves : 0}]&nbsp;&nbsp; <span className="contextbutton fa fa-ellipsis-h" id="AutherCluster" onClick={() => {
                                                    this.setState({
                                                        visibility: this.state.visibility == "none" ? "block" : "none",
                                                        id: data.id,
                                                    })
                                                }} style={{ cursor: "pointer" }}></span>
                                            </div>
                                            <div id={data.id} style={{ marginTop: "20px", display: [data.id == this.state.id ? this.state.visibility : "none"] }} >{data.long_description}<div style={{ backgroundColor: "#DCDCDC" }}>{data.citation}</div></div>
                                        </div></div>);
                                    })
                                }
                            </div>
                        </div>
                        <div className="col-xs-12 col-sm-12 col-md-2" style={{ borderRadius: "3px", backgroundColor: "white", height: "89vh", overflow: "auto", borderRight: "1px solid black", borderLeft: "2px solid black", textAlign: "" }}>
                            {this.state.sortedAuthorClusterDetail && this.state.sortedAuthorClusterDetail.length > 0 && this.state.sortedAuthorClusterDetail.map(data => {
                                if (!(arr.includes(data.authorId))) {
                                    arr.push(data.authorId);
                                    return (
                                        <div style={{ padding: " 0px 10px", width: "160px", margin: "auto" }}><a onClick={() => {
                                            this.props.setClickedState(
                                                {
                                                    authorClicked: true,
                                                    conceptClicked: false
                                                }
                                            )

                                        }} style={{ color: "black", textDecoration: "none", cursor: "pointer" }} href={"#" + data.authorLastName}><b>{(data.authorLastName && data.authorFirstName) ? (data.authorLastName + ", " + data.authorFirstName) : (data.authorLastName ? data.authorFirstName : (data.authorFirstName ? data.authorFirstName : ""))}</b></a></div>
                                    );
                                }
                            })}
                        </div>
                        <div className="col-xs-12 col-sm-12 col-md-3" style={{ borderRadius: "3px", backgroundColor: "white", height: "89vh", overflow: "auto", borderLeft: "0px", textAlign: "center" }}>
                            <div className="text-center">
                                This place is for adds
                        </div>
                        </div>
                    </div>
                    }
                </div>}
            </div>
        );
    }
}
