const mapDispatchToProps = (dispatch) => {
    return {
        //getAutherClusterDetails: bindActionCreators(getAutherClusterDetails, dispatch),
        //getAutherClusterDetailsViaSupra: bindActionCreators(getAutherClusterDetailsViaSupra, dispatch),
        flushAuthorClusterState: bindActionCreators(flushAuthorClusterState, dispatch),
        setSelectedCategory: bindActionCreators(setSelectedCategory, dispatch),
        setSelectedItem: bindActionCreators(setSelectedItem, dispatch),
        getAuthorCluster: bindActionCreators(getAuthorCluster, dispatch),
        addTag: bindActionCreators(addTag, dispatch),
        setClickedState: bindActionCreators(setClickedState, dispatch),
        increasePerspectiveLike:bindActionCreators(increasePerspectiveLike,dispatch),
        decreasePerspectiveLike:bindActionCreators(decreasePerspectiveLike,dispatch)
    }
}
const mapStateToProps = (state) => {
    // console.log("state in mapStateToProps", state);
    return {
        navbar: state.navbar,
        autherCluster: state.autherCluster,
        sortedConceptsNames: getUniqueSortedConceptNameFromAuthorCluster(state),
        dynamicSortedAuthorCluster:getDynamicSortedAuthorClusterWithDetails(state),
        sortedAuthorClusterDetail: getSortedAuthorClusterWithDetailsByAuthorLast(state)
    }
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AutherCluster));