import React, { Component } from 'react';
import "./AuthorPage.css";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from "react-router-dom";
import { setSelectedCategory, setSelectedItem, flushState, addTag } from "../../actions/navbaractions";
import { flushAuthorClusterState } from '../../actions/autherCluster';
import { getAuthorGroups } from "../../utilities";
import { getUniqueConcept, getSortedPerspectivesByConceptName, getAuthorBioData, getUniqueAuthorBooks } from '../../selectors';
import queryString from "query-string";
import Axios from 'axios';
import ReactLoading from "react-loading";
import { getAuthorDetails, getAuthorInfluencedByAuthors,flushAuthorState,increasePerspectiveLike,decreasePerspectiveLike } from "../../actions/authorActions";
class AuthorPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            query: "",
            selectedOption: this.props.navbar.selectedOption,
            // apiLoading: this.props.author.apiLoading,
            // apiServerError: this.props.author.apiServerError,
            // apiNetworkError: this.props.author.apiNetworkError,
            //authorsWithDetails: this.props.author.authorsWithDetails,
            // author_authors_apiLoading: this.props.author.author_authors_apiLoading,
            // author_authors_apiServerError: this.props.author.author_authors_apiServerError,
            // author_authors_apiNetworkError: this.props.author.author_authors_apiNetworkError,
            author_authors: this.props.author.author_authors,
            authorInfluencedByAuthors: this.props.author.authorInfluencedByAuthors,
            authorInfluencedOnAuthors: this.props.author.author_authors.AuthorInfluenceAuthors,
            authorConvoWithAuthors: this.props.author.author_authors.AuthorConvoAuthors,
            id: "",
            visibility: "none",
            showBooks: false,
            sortedAuthorInfluencedByAuthors: setInitialsortedAuthorInfluencedByAuthors(this.props.author.authorInfluencedByAuthors),
            sortedAuthorInfluencedOnAuthors: setInitialsortedAuthorInfluencedOnAuthors(this.props.author.author_authors.AuthorInfluenceAuthors),
            sortedAuthorConvoWithAuthors: setInitialsortedAuthorConvoWithAuthors(this.props.author.author_authors.AuthorConvoAuthors),
            index: 0,
            index1: 0,
            index2: 0,
            loading: this.props.author.loading,
            //normalized states
            //authorBioHeadings: this.props.author.authorBioHeadings,
            //authorGroups: this.props.author.authorGroups,
            authorPerspectives: this.props.author.authorPerspectives,
            author: this.props.author.author,
            concepts: this.props.author.concepts,
            UniqueConcepts: this.props.uniqueconcepts,
            sortedPerspectives: this.props.sortedperpectives,
            authorBio: this.props.authorBio,
            authorBooksById: this.props.authorBooksById,
            authorBooks: this.props.author.authorBooks,
            loadImage:"none"
            //authorOnConcepts:this.props.author.concept
            //end normalized states
        }
        this.getSortedAuthorInfluencedByAuthors = this.getSortedAuthorInfluencedByAuthors.bind(this);
        //this.getSortedAuthorInfluencedOnAuthors = this.getSortedAuthorInfluencedOnAuthors.bind(this);
        //this.getSortedAuthorConvoWithAuthors = this.getSortedAuthorConvoWithAuthors.bind(this);
        this.reverse = this.reverse.bind(this);
        this.forward = this.forward.bind(this);
        this.reverse1 = this.reverse1.bind(this);
        this.forward1 = this.forward1.bind(this);
        this.reverse2 = this.reverse2.bind(this);
        this.forward2 = this.forward2.bind(this);
        this.onAuthorClick = this.onAuthorClick.bind(this);
        this.onConceptClick = this.onConceptClick.bind(this);
        this.onBioClick = this.onBioClick.bind(this);
        this.onLikeClick=this.onLikeClick.bind(this);
        if (this.props.navbar.selectedOption) {
            if (this.props.navbar.selectedOption.id) {
                if (this.props.navbar.selectedOption.category == "Authors") {
                    this.props.getAuthorDetails(this.props.navbar.selectedOption.id);
                    this.props.getAuthorInfluencedByAuthors(this.props.navbar.selectedOption.id);
                    //  this.props.getAuthorInfluencedOnAuthors(this.props.navbar.selectedOption.id);
                    //  this.props.getAuthorConvoWithAuthors(this.props.navbar.selectedOption.id);
                }
            }
        }
    }
    getAuthor = (author) => {
        return new Promise((resolve, reject) => {
            Axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/author`, { author }).then(data => {
                if (data.data.obj) {
                    let result = data.data.obj.selectedOption;
                    resolve(result);
                }
                resolve(null);
            }).catch(err => {
                console.log(err);
            });
        });
    }
    async componentWillMount() {
        if (this.props.match.params.name) {
            if (this.props.match.params.name && queryString.parse(this.props.location.search).filters) {
                let filters = [];
                let queryFilters = queryString.parse(this.props.location.search).filters;
                if (typeof queryFilters === "string") {
                    filters.push(queryFilters);
                } else {
                    filters = queryFilters;
                }
                let author_group = await getAuthorGroups(filters);
                let author = {};
                author.name = this.props.match.params.name;
                author.groupIds = author_group.map(group => group.id);
                let result = await this.getAuthor(author);
                if (result) {
                    this.props.setSelectedItem(result);
                    this.setState({
                        selectedOption: result
                    });
                    this.props.addTag(author_group);
                } else {
                    this.props.history.push("/Message");
                }
            } else {
                let author = {};
                author.name = this.props.match.params.name;
                author.groupIds = [];
                let result = await this.getAuthor(author);
                this.props.setSelectedItem(result);
                this.setState({
                    selectedOption: result
                });
                this.props.getAuthorDetails(this.state.selectedOption.id);
                this.props.getAuthorInfluencedByAuthors(this.state.selectedOption.id);

            }
        }
    }
    async componentWillReceiveProps(nextProps) {

        if (this.props.navbar.tags.length !== nextProps.navbar.tags.length) {
            if (nextProps.navbar.selectedOption != undefined) {
                let author = {};
                author.name = nextProps.navbar.selectedOption.value;
                author.groupIds = nextProps.navbar.tags.map(x => x.id);
                let result = await this.getAuthor(author);
                if (result) {
                    this.props.setSelectedItem(result);
                    this.setState({
                        selectedOption: result
                    });
                    this.props.getAuthorDetails(this.state.selectedOption.id);
                    this.props.getAuthorInfluencedByAuthors(this.state.selectedOption.id);
                } else {
                    this.props.history.push("/Message");
                }

            }
        }
        if (this.state.selectedOption != nextProps.navbar.selectedOption) {
            this.props.flushAuthorState();
            if (nextProps.navbar.selectedOption.id && nextProps.navbar.selectedOption.category == "Authors") {
                this.props.getAuthorDetails(nextProps.navbar.selectedOption.id);
                //this.props.getAuthorBio(nextProps.navbar.selectedOption.id);
                //this.props.getAuthorBooks(nextProps.navbar.selectedOption.id);
                this.props.getAuthorInfluencedByAuthors(nextProps.navbar.selectedOption.id);
                //  this.props.getAuthorInfluencedOnAuthors(nextProps.navbar.selectedOption.id);
                //  this.props.getAuthorConvoWithAuthors(nextProps.navbar.selectedOption.id);
            }
            this.setState({
                selectedOption: nextProps.navbar.selectedOption,
                apiLoading: nextProps.author.apiLoading,
                apiServerError: nextProps.author.apiServerError,
                apiNetworkError: nextProps.author.apiNetworkError,
                //authorsWithDetails: nextProps.author.authorsWithDetails,
                author_authors_apiLoading: nextProps.author.author_authors_apiLoading,
                author_authors_apiServerError: nextProps.author.author_authors_apiServerError,
                author_authors_apiNetworkError: nextProps.author.author_authors_apiNetworkError,
                author_authors: nextProps.author.author_authors,
                authorInfluencedByAuthors: nextProps.author.authorInfluencedByAuthors,
                authorInfluencedOnAuthors: nextProps.author.author_authors.AuthorInfluenceAuthors,
                authorConvoWithAuthors: nextProps.author.author_authors.AuthorConvoAuthors,
                sortedAuthorInfluencedByAuthors: setInitialsortedAuthorInfluencedByAuthors(nextProps.author.authorInfluencedByAuthors),
                sortedAuthorInfluencedOnAuthors: setInitialsortedAuthorInfluencedOnAuthors(nextProps.author.author_authors.AuthorInfluenceAuthors),
                sortedAuthorConvoWithAuthors: setInitialsortedAuthorConvoWithAuthors(nextProps.author.author_authors.AuthorConvoAuthors),
                //normalized states
                //authorBioHeadings: nextProps.author.authorBioHeadings,
                //authorGroups: nextProps.author.authorGroups,
                authorPerspectives: nextProps.author.authorPerspectives,
                author: nextProps.author.author,
                concepts: nextProps.author.concepts,
                //authorOnConcepts:nextProps.author.concept
                UniqueConcepts: nextProps.uniqueconcepts,
                sortedPerspectives: nextProps.sortedperpectives,
                authorBio: nextProps.authorBio,
                authorBooksById: nextProps.authorBooksById,
                authorBooks: nextProps.author.authorBooks
                //end normalized states

            })
            // console.log("Here i m")

        }
        else {
            this.setState({
                //authorsWithDetails: nextProps.author.authorsWithDetails,
                apiLoading: nextProps.author.apiLoading,
                apiServerError: nextProps.author.apiServerError,
                apiNetworkError: nextProps.author.apiNetworkError,
                author_authors_apiLoading: nextProps.author.author_authors_apiLoading,
                author_authors_apiServerError: nextProps.author.author_authors_apiServerError,
                author_authors_apiNetworkError: nextProps.author.author_authors_apiNetworkError,
                author_authors: nextProps.author.author_authors,
                authorInfluencedByAuthors: nextProps.author.authorInfluencedByAuthors,
                authorInfluencedOnAuthors: nextProps.author.author_authors.AuthorInfluenceAuthors,
                authorConvoWithAuthors: nextProps.author.author_authors.AuthorConvoAuthors,
                //sortedAuthorInfluencedByAuthors: setInitialsortedAuthorInfluencedByAuthors(nextProps.author.authorInfluencedByAuthors),
                sortedAuthorInfluencedOnAuthors: setInitialsortedAuthorInfluencedOnAuthors(nextProps.author.author_authors.AuthorInfluenceAuthors),
                sortedAuthorConvoWithAuthors: setInitialsortedAuthorConvoWithAuthors(nextProps.author.author_authors.AuthorConvoAuthors),
                //normalized states
                //authorBioHeadings: nextProps.author.authorBioHeadings,
                //authorGroups: nextProps.author.authorGroups,
                authorPerspectives: nextProps.author.authorPerspectives,
                author: nextProps.author.author,
                concepts: nextProps.author.concepts,
                UniqueConcepts: nextProps.uniqueconcepts,
                sortedPerspectives: nextProps.sortedperpectives,
                authorBio: nextProps.authorBio,
                authorBooksById: nextProps.authorBooksById,
                authorBooks: nextProps.author.authorBooks
                //authorOnConcepts:nextProps.author.concept
                //end normalized states

            })
        }
        // console.log("Author State", this.state);

    }
    onLikeClick(data) {
        if (this.state.authorPerspectives[data.id].isLike) {
            this.props.decreasePerspectiveLike(data.id);
        }
        else {
            this.props.increasePerspectiveLike(data.id);
        }
    }
    onAuthorClick(author) {
        let label = author.firstName + " " + author.lastName;
        let value = label;
        let id = author.id;
        let born = author.dob;
        let died = author.dod;
        let category = "Authors";
        this.props.flushAuthorState();
        console.log(category);
        this.props.setSelectedItem({ label, value, id, born, died, category });

    }
    onBioClick(bio) {
        let label = bio.distinct_grouping;
        let value = label;
        let id = bio.distinct_grouping_id;
        let category = "Author-Groups"
        this.props.flushAuthorState();
        this.props.flushAuthorClusterState();
        this.props.setSelectedItem({ label, value, id, category, via: "group" });
    }
    onConceptClick(concept) {

        let label = this.state.concepts[concept.Concept].name;
        let value = label;
        let id = concept.ConceptId;
        let category = "Concepts"
        this.props.flushAuthorState();
        this.props.setSelectedItem({ label, value, id, category });
    }
    // getSortedAuthorConvoWithAuthors(index) {
    //     let rtnArray = [];
    //     rtnArray = this.state.authorConvoWithAuthors.slice(index, index + 3);
    //     // console.log("Slice params", index, index + 3);
    //     // console.log("Rtn array i", rtnArray);
    //     this.setState({
    //         sortedAuthorConvoWithAuthors: rtnArray,
    //     })
    // }
    getSortedAuthorInfluencedByAuthors(index) {
        let rtnArray = [];
        rtnArray = this.state.authorInfluencedByAuthors.slice(index, index + 3);
        // console.log("Slice params", index, index + 3);
        // console.log("Rtn array i", rtnArray);
        this.setState({
            sortedAuthorInfluencedByAuthors: rtnArray,
        })
    }
    // getSortedAuthorInfluencedOnAuthors(index) {
    //     let rtnArray = [];
    //     rtnArray = this.state.authorInfluencedOnAuthors.slice(index, index + 3);
    //     // console.log("Slice params", index, index + 3);
    //     // console.log("Rtn array i", rtnArray);
    //     this.setState({
    //         sortedAuthorInfluencedOnAuthors: rtnArray,
    //     })
    // }
    reverse() {
        let index = this.state.index;
        // console.log("Index reverse", index);
        if (index >= 3) {
            this.setState({ index: (index - 3) })
            this.getSortedAuthorInfluencedByAuthors(index - 3);
        }

    }
    reverse1() {
        let index = this.state.index1;
        // console.log("Index reverse", index);
        if (index >= 3) {
            this.setState({ index1: (index - 3) })
            this.getSortedAuthorInfluencedOnAuthors(index - 3);
        }

    }
    reverse2() {
        let index = this.state.index2;
        // console.log("Index reverse", index);
        if (index >= 3) {
            this.setState({ index2: (index - 3) })
            this.getSortedAuthorConvoWithAuthors(index - 3);
        }

    }
    forward() {
        let totalLength = this.state.authorInfluencedByAuthors.length;
        let index = this.state.index;
        // console.log("Index forward", index);
        if (index + 3 < totalLength) {
            this.setState({ index: (index + 3) })
            this.getSortedAuthorInfluencedByAuthors(index + 3);
        }

    }
    forward1() {
        let totalLength = this.state.authorInfluencedOnAuthors.length;
        let index = this.state.index1;
        // console.log("Index forward", index);
        if (index + 3 < totalLength) {
            this.setState({ index1: (index + 3) })
            this.getSortedAuthorInfluencedOnAuthors(index + 3);
        }

    }
    forward2() {
        let totalLength = this.state.authorConvoWithAuthors.length;
        let index = this.state.index2;
        // console.log("Index forward", index);
        if (index + 3 < totalLength) {
            this.setState({ index2: (index + 3) })
            this.getSortedAuthorConvoWithAuthors(index + 3);
        }
    }

    render() {
        return (
            <div className="overflowhidden " style={{ overflow: "hidden" }}>
                {this.state.selectedOption.id && this.state.selectedOption.category == "Authors" && <div className="container-fluid">
                    {this.state.apiLoading && <div style={{ width: "100px", margin: "auto", marginTop: "30vh" }}>
                        <ReactLoading type={"balls"} color={"red"} />
                    </div>}
                    {!this.state.apiLoading && <div>
                        {!this.state.showBooks && <div className="row">
                            <div className="col-xs-12 col-sm-12 col-md-3 pt-5" style={{ borderRadius: "3px", backgroundColor: "white", height: "89vh", overflow: "auto", border: "3px  solid white", textAlign: "" }}>
                                {
                                    <div style={{ width: "150px", margin: "auto" }}>
                                        {this.state.UniqueConcepts && this.state.UniqueConcepts.length > 0 && this.state.UniqueConcepts.map(concept => {
                                            if (concept.discrete_author_name != undefined) {
                                                return <b><a href={"#" + concept.discrete_author_name_id} style={{ cursor: "pointer", textDecoration: "none", color: "black", display: "block" }} onClick={() => {
                                                }}>{concept.discrete_author_name}</a></b>
                                            }

                                        })}
                                    </div>
                                }
                            </div>
                            <div className="col-xs-12 col-sm-12 col-md-4" style={{ borderRadius: "3px", backgroundColor: "white", height: "89vh", overflow: "auto" }}>
                                <div id="main-content" style={{ backgroundColor: "white" }}>
                                   <div id="img" style={{ wheight:"280px",display:this.state.loadImage }}>
                                        {/*<h4 style={{ zIndex: "3", padding: "10px", color: "white" }} className="pull-right">{this.state.selectedOption ? this.state.selectedOption.label : ""}</h4>
                                        <h5 style={{ zIndex: "3", padding: "10px", color: "white", display: "inline-block" }} className="float-right">{(this.state.selectedOption.born && this.state.selectedOption.died) ? this.state.selectedOption.born + " - " + this.state.selectedOption.died : "HUL"}</h5>
                                        */}
                                        <img src={this.state.author.pictureLink} onError={() => {this.setState({loadImage:"none"})}} onLoad={() => {this.setState({loadImage:"block"});}} alt={this.state.selectedOption.label} style={{ width: "100%",height:"100%",display:this.state.loadImage }} />                                    </div>
                                    {/* <div style={{ marginTop: "10px", border: "1px solid white", paddingTop: "5px", borderRadius: "5px", width: "398px", textAlign: "center", backgroundColor: "white" }}>
                                        <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                            <input type="checkbox" value="" />Positive
                                        </label>
                                        <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                            <input type="checkbox" value="" />Critical
                                        </label>
                                        <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                            <input type="checkbox" value="" />Funny
                                        </label>
                                    </div> */}
                                    {
                                        this.state.sortedPerspectives && this.state.sortedPerspectives.length > 0 && this.state.sortedPerspectives.map(data => {
                                            return (<div id={data.ConceptId}><div className="clearfix" style={{ marginTop: "10px", backgroundColor: "white", padding: "10px", border: "1px solid white", borderRadius: "5px" }} >
                                                <b style={{ color: "blue", cursor: "pointer",textTransform:'capitalize' }} onClick={() => this.onConceptClick(data)}>{data.pronoun}{" "} {this.state.concepts[data.Concept].name}</b>{" is "}{data.description}
                                                {/* <div style={{ color: "red" }}>{this.state.author.firstName}{" "}{this.state.author.lastName}</div> */}
                                                <div className="text-right">
                                                    <i style={{ color:data.isLike?"red":"grey",fontSize:'20px' }} className="fa fa-thumbs-up " onClick={() => { this.onLikeClick(data) }}></i>&nbsp;[{data.loves ? data.loves : "0"}]&nbsp;&nbsp; <span className="contextbutton fa fa-ellipsis-h" id="AuthorEllipseCircle" onClick={() => {
                                                        this.setState({
                                                            visibility: this.state.visibility == "none" ? "block" : "none",
                                                            id: data.id,
                                                        })
                                                    }} style={{ cursor: "pointer" }}></span>
                                                </div>
                                                <div id={data.id} style={{ marginTop: "20px", display: [data.id == this.state.id ? this.state.visibility : "none"],textAlign:'justify' }} >{data.longDescription}<div style={{ backgroundColor: "#DCDCDC" }}>{data.citation}</div></div>
                                            </div></div>);
                                        })
                                    }
                                </div>
                            </div>
                            <div className="col-xs-12 col-sm-12 col-md-3 " style={{ borderRadius: "3px", backgroundColor: "white", height: "89vh", overflow: "auto", borderLeft: "1px solid black" , borderRight: "1px solid black" }}>
                                <h3><b>{this.state.author.firstName} {this.state.author.lastName}</b></h3>
                                <h6>{this.state.author.dob}-{this.state.author.dod}</h6>
                                <button className="btn btn-link" style={{color:'black'}} onClick={() => {this.setState({showBooks:true})}}><i className="fas fa-book-open"></i> <span><b>({this.state.author.Books && this.state.author.Books.length})</b></span></button>
                                <br /><br />
                                {this.state.authorBio && this.state.authorBio.length > 0 && this.state.authorBio.map(item => {
                                    return (<div className="" style={{ width: "340px",}}><b style={{fontSize:15,display:"block"}}>{item.heading.charAt(0).toUpperCase() + item.heading.slice(1)}:</b>
                                        {item.values.map(v => {

                                            return <p6 style={{marginTop:'-10px;'}}><span style={{ display: "inline-block",padding:0.1 }}><Link style={{ cursor: "pointer",color:'black',fontSize:13 }} to="author-clusters" onClick={() => this.onBioClick({ distinct_grouping: v.name, distinct_grouping_id: v.id })}>{v.name}</Link>&nbsp;&nbsp;</span></p6>
                                        })}

                                    </div>);
                                })
                                }
                                <div id="authorInfluencedByAuthors" style={{ width: "340px", margin: "auto" }}>
                                    {/*this.state.sortedAuthorInfluencedByAuthors && this.state.sortedAuthorInfluencedByAuthors.length > 0 && <div>
                                        <h6 style={{ color: "red" }}>Influenced By:</h6>
                                        <div className="dytb" >
                                            <i style={{ cursor: "pointer" }} onClick={() => this.reverse()} className="fa fa-angle-left dytbc" />
                                            {this.state.sortedAuthorInfluencedByAuthors.map(author => {
                                                return (
                                                    <div onClick={() => this.onAuthorClick(author)} style={{ marginRight: "8px", display: "inline-block", marginLeft: "8px", width: "85px", maxHeight: "180px", border: "1px solid white", borderRadius: "3px", cursor: "pointer" }}>
                                                        <div style={{ width: "83px", minHeight: "70px", backgroundColor: "yellow" }}></div>
                                                        <div style={{ fontSize: "13px", width: "83px", overflow: "hidden", textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>{author.author_first + author.author_last}</div>
                                                    </div>
                                                );
                                            })}
                                            <i style={{ cursor: "pointer" }} onClick={() => this.forward()} className="fa fa-angle-right dytbc" ></i>
                                        </div>
                                    </div>*/
                                    }
                                    {this.state.sortedAuthorInfluencedOnAuthors && this.state.sortedAuthorInfluencedOnAuthors.length > 0 && <div>
                                        <h6 style={{ color: "red" }}>Influenced On:</h6>
                                        <div className="dytb">
                                            <i style={{ cursor: "pointer" }} onClick={() => this.reverse1()} className="fa fa-angle-left dytbc" />
                                            {this.state.sortedAuthorInfluencedOnAuthors.map(author => {
                                                return (
                                                    <div onClick={() => this.onAuthorClick(author)} style={{ marginRight: "8px", display: "inline-block", marginLeft: "8px", width: "85px", maxHeight: "180px", border: "1px solid white", borderRadius: "3px", cursor: "pointer" }}>
                                                        <div style={{ width: "83px", minHeight: "70px", backgroundColor: "yellow" }}>
                                                            <img src={`https://conceptionary-images.s3.amazonaws.com/${author.lastName}+${author.firstName}.jpg`} alt={author.firstName + " " + author.lastName}></img>
                                                        </div>
                                                        <div style={{ fontSize: "13px", width: "83px", overflow: "hidden", textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>{author.firstName + author.lastName}</div>
                                                    </div>
                                                );
                                            })}
                                            <i style={{ cursor: "pointer" }} onClick={() => this.forward1()} className="fa fa-angle-right dytbc"></i>
                                        </div>
                                    </div>
                                    }
                                    {this.state.sortedAuthorConvoWithAuthors && this.state.sortedAuthorConvoWithAuthors.length > 0 && <div>
                                        <h6 style={{ color: "red" }}>In Conversation with:</h6>
                                        <div className="dytb">
                                            <i style={{ cursor: "pointer" }} onClick={() => this.reverse2()} className="fa fa-angle-left dytbc" />
                                            {this.state.sortedAuthorConvoWithAuthors.map(author => {

                                                return (
                                                    <div onClick={() => this.onAuthorClick(author)} style={{ marginRight: "8px", display: "inline-block", marginLeft: "8px", width: "85px", maxHeight: "180px", border: "1px solid white", borderRadius: "3px", cursor: "pointer" }}>
                                                        <div style={{ width: "83px", minHeight: "70px", backgroundColor: "yellow" }}>
                                                            <img src={`https://conceptionary-images.s3.amazonaws.com/${author.lastName}+${author.firstName}.jpg`} alt={author.firstName + " " + author.lastName}></img>
                                                        </div>
                                                        <div style={{ fontSize: "13px", width: "83px", overflow: "hidden", textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>{author.firstName + author.lastName}</div>
                                                    </div>
                                                );
                                            })}
                                            <i style={{ cursor: "pointer" }} onClick={() => this.forward2()} className="fa fa-angle-right dytbc"></i>
                                        </div>
                                    </div>
                                    }
                                </div>
                            </div>

                            <div className="col-xs-12 col-sm-12 col-md-2" style={{ borderRadius: "3px", backgroundColor: "white", height: "89vh", overflow: "auto", borderLeft: "0px", textAlign: "center", cursor: "pointer" }}>
                                <div className="text-center">
                                    This place is for adds
                            </div>
                            </div>
                        </div>
                        }
                        {/* new row for books */}
                        {this.state.showBooks && <div className="row">
                            <div className="col-xs-12 col-sm-12 col-md-3" style={{ borderRadius: "3px", backgroundColor: "white", height: "89vh", overflow: "auto", textAlign: "" }}>
                                <div style={{ width: "150px", margin: "auto", marginTop: "10px" }}>
                                    <div style={{ cursor: "pointer" }} onClick={() => this.setState({ showBooks: false })}><i style={{ width: "16px" }} className="fa  fa-arrow-left" />&nbsp;&nbsp;<span style={{ fontSize: "16px" }} >Go Back</span></div>
                                    <h3>Books</h3>
                                    {this.state.authorBooksById && this.state.authorBooksById.length > 0 && this.state.authorBooksById.map(book => {
                                        return (
                                            <a href={"#" + book.discrete_book_id} style={{ cursor: "pointer", textDecoration: "none", color: "black", display: "block" }} onClick={() => {
                                            }}>{book.title}</a>
                                        )
                                    })
                                    }
                                </div>
                            </div>
                            <div className="col-xs-12 col-sm-12 col-md-4" style={{ borderRadius: "3px", backgroundColor: "white", height: "89vh", overflow: "auto" }}>
                                <div id="main-content" style={{ backgroundColor: "white" }}>
                                    <div id="img" style={{ width: "398px", height: "280px", backgroundColor: "red" }}>
                                        <h4 style={{ zIndex: "3", padding: "10px", color: "white" }} className="pull-right">{this.state.selectedOption ? this.state.selectedOption.label : ""}</h4>
                                        <h5 style={{ zIndex: "3", padding: "10px", color: "white", display: "inline-block" }} className="float-right">{(this.state.selectedOption.born && this.state.selectedOption.died) ? this.state.selectedOption.born + " - " + this.state.selectedOption.died : "HUL"}</h5>
                                    </div>
                                    {/* <div style={{ marginTop: "10px", border: "1px solid white", paddingTop: "5px", borderRadius: "5px", width: "398px", textAlign: "center", backgroundColor: "white" }}>
                                        <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                            <input type="checkbox" value="" />Positive
                                    </label>
                                        <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                            <input type="checkbox" value="" />Critical
                                    </label>
                                        <label class="checkbox-inline" style={{ marginRight: "10px" }}>
                                            <input type="checkbox" value="" />Funny
                                    </label>
                                    </div> */}
                                    {this.state.author && this.state.author.Books && this.state.author.Books.length > 0 && this.state.author.Books.map(book => {
                                        {
                                            return this.state.authorBooks[book].BookDescriptions && this.state.authorBooks[book].BookDescriptions.length > 0 && this.state.authorBooks[book].BookDescriptions.map(bookDescription => {
                                                return (<div id={book} className="clearfix" style={{ marginTop: "10px", backgroundColor: "white", padding: "10px", border: "1px solid white", borderRadius: "5px" }} >
                                                    <div className="row">
                                                        <div className="col col-md-2">
                                                            <div style={{ width: "60px", minHeight: "80px", background: "red" }}></div>
                                                        </div>
                                                        <div className="col col-md-10">
                                                            <h6>{this.state.authorBooks[book].title}</h6>
                                                            <div style={{ color: "black" }}>{bookDescription.description}</div>
                                                            <div className="pull-right">
                                                                <a className="contextbutton" onClick={() => {
                                                                    this.setState({
                                                                        visibility: this.state.visibility == "none" ? "block" : "none",
                                                                        id: book.id,
                                                                    })
                                                                }} style={{ cursor: "pointer" }}>Context</a>
                                                            </div>
                                                            <div id={book} style={{ marginTop: "20px", display: [book == this.state.id ? this.state.visibility : "none"] }} >{bookDescription.source}</div>
                                                        </div>
                                                    </div>
                                                </div>);
                                            })
                                        }
                                    })}
                                </div>
                            </div>
                        </div>
                        }
                    </div>
                    }
                </div>}

            </div>
        );
    }
}
