const mapDispatchToProps = (dispatch) => {
    return {
        setSelectedCategory: bindActionCreators(setSelectedCategory, dispatch),
        setSelectedItem: bindActionCreators(setSelectedItem, dispatch),
        getAuthorDetails: bindActionCreators(getAuthorDetails, dispatch),
        // getAuthorBio: bindActionCreators(getAuthorBio, dispatch),
        //getAuthorBooks: bindActionCreators(getAuthorBooks, dispatch),
        getAuthorInfluencedByAuthors: bindActionCreators(getAuthorInfluencedByAuthors, dispatch),
        // getAuthorInfluencedOnAuthors: bindActionCreators(getAuthorInfluencedOnAuthors, dispatch),
        //getAuthorConvoWithAuthors: bindActionCreators(getAuthorConvoWithAuthors, dispatch),
        flushAuthorState: bindActionCreators(flushAuthorState, dispatch),
        flushAuthorClusterState: bindActionCreators(flushAuthorClusterState, dispatch),
        addTag: bindActionCreators(addTag, dispatch),
        increasePerspectiveLike:bindActionCreators(increasePerspectiveLike,dispatch),
        decreasePerspectiveLike:bindActionCreators(decreasePerspectiveLike,dispatch)
    }
}
const mapStateToProps = (state) => {
    return {
        navbar: state.navbar,
        author: state.author,
        uniqueconcepts: getUniqueConcept(state),
        sortedperpectives:  getSortedPerspectivesByConceptName(state),
        authorBio: getAuthorBioData(state),
        authorBooksById: getUniqueAuthorBooks(state)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AuthorPage);