import {schema} from 'normalizr';
const authorBioHeading=new schema.Entity('AuthorBioHeadings')
const authorGroup= new schema.Entity('AuthorGroups',{
    AuthorBioHeading:authorBioHeading
});
const book=new schema.Entity("books")
const concept=new schema.Entity('concepts');
const perspective=new schema.Entity('perspectives',{
    Concept:concept
})
export const authorSchema=new schema.Entity("authors",{
    Perspectives:[perspective],
    AuthorGroups:[authorGroup],
    Books:[book]
});