import React, { Component } from 'react';
import "./Signup.css";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { userSignupRequest } from "../../actions/signupActions";
import classnames from "classnames";
import { Redirect } from "react-router";
import validateSignup from "../../helpers/validateSignup";
class Signup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            firstname: "",
            lastname: "",
            email: "",
            errors: {},
            isLoading: false,
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        // console.log("Errors in Signup State", this.state.errors);
    }
    onChange(e) {
        this.setState({
            [e.target.name]: e.target.value,
        });
    }
    isValid(data) {
        const { errors, isValid } = validateSignup(data);
        if (!isValid) {
            this.setState({ errors })
        }
        return isValid;
    }
    onSubmit(e) {
        e.preventDefault();
        if (this.isValid(this.state)) {
            this.setState({ errors: {}, isLoading: true });
            this.props.userSignupRequest({
                fname: this.state.firstname, lname: this.state.lastname, email: this.state
                    .email
                , call: "signup"
            }).then(
                ({ data }) => { this.props.history.push("/login"); console.log("**********first Function*******:", data.body); console.log("State in good 200 request", this.state); },
                (data) => {
                    // console.log("**********second Function*******:", data.response.data);
                    this.setState({ errors: data.response.data, isLoading: false })
                    // console.log("State in bad request", this.state);
                }
            );
        }
    }
    render() {
        const { errors } = this.state;
        return (
            <div>
                <div className="back">


                    <div className="col col-md-6 offset-md-3 div-center" style={{ marginTop: '50px' }}>
                        <div className="loginCenter">
                            <h3>Signup</h3>
                            <hr />
                            <form onSubmit={this.onSubmit}>
                                <div className={classnames("form-group", { "has-error": errors.firstname })}>
                                    {errors.firstname && <span className="help-block">{errors.firstname}</span>}
                                    {!errors.firstname && <label >Name</label>}
                                    <input type="text" class="form-control" value={this.state.firstname} onChange={this.onChange} name="firstname" />
                                </div>
                                <div className="form-group">
                                    {errors.lastname && <span className="help-block">{errors.lastname}</span>}
                                    {!errors.lastname && <label >Last Name</label>}
                                    <input type="text" class="form-control" value={this.state.lastname} onChange={this.onChange} name="lastname" />
                                </div>
                                <div className="form-group">
                                    {errors.email && <span className="help-block">{errors.email}</span>}
                                    {!errors.email && <label>Email</label>}
                                    <input type="email" class="form-control" value={this.state.email} onChange={this.onChange} name="email" email="email" />
                                </div>
                                <button className="pull-right" disabled={this.state.isLoading} style={{ backgroundColor: '#009dd6' }} type="submit" class="btn">Get Verified</button>
                                <a href="/login" class="btn text-center btn-link">Already have and account ?</a>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        userSignupRequest: bindActionCreators(userSignupRequest, dispatch),
    }
}

export default connect(null, mapDispatchToProps)(Signup);
