import { stat } from "fs";

const initialState = {
    listRelatedData: [],
    loading: true,
    apiLoading: false,
    apiServerError: false,
    apiNetworkError: false,
    perspectiveIds: [],
    sortedAuthorIds: [],
    perspectivesRelations: {},
    perspectivesDetail: [],
    perspectivesByAuthor: {},
    sortedAuthors: [],
    authors: {},
    concept: {},
    keywordIds: [],
    keywords: {},
    isLike: false,
    initial:false
}
export default (state = initialState, action = {}) => {
    switch (action.type) {
        case 'SERVER_ERROR':
            return {
                apiServerError: true,
            }
        case 'NETWORK_ERROR':
            return {
                ...state,
                apiNetworkError: true,
            }
        case 'REQUEST_STARTED':
            return {
                apiLoading: true,
            }
        case 'REQUEST_SUCCESSFUL':
            return {
                ...state,
                apiLoading: false,
                apiServerError: false,
                apiNetworkError: false,
            }
        case 'SET_CONCEPT_DETAILS': {
            return {
                ...state,
                perspectivesRelations: action.payload.perspectivesRelations,
                authors: action.payload.authors,
                concept: action.payload.concepts,
                keywords: action.payload.keywords,
                perspectivesByAuthor: action.payload.perspectivesByAuthor,
                sortedAuthorIds: action.payload.sortedAuthorIds,
                sortedAuthors: action.payload.sortedAuthors,
            }
        }
        case 'SET_DETAILED_PERSPECTIVE_BY_SCROLL': {
            let perspectivesByAuthor = state.perspectivesByAuthor;
            action.payload.perspectives.forEach(p => {
                perspectivesByAuthor[p.AuthorId].push(p);
            })
            return {
                ...state,
                perspectivesByAuthor: perspectivesByAuthor
            }
        }
        case 'SET_DETAILED_PERSPECTIVE_BY_AUTHOR': {
            return {
                ...state,
                perspectivesByAuthor: action.payload.perspectivesByAuthor,
                initial:true
            }
        }
        case 'SET_DETAILED_PERSPECTIVE_BY_KEYWORDS': {
            return {
                ...state,
                perspectivesByAuthor: action.payload.perspectivesByAuthor,
                initial:true
            }
        }
        case 'setPerspectiveIds': {
            return {
                ...state,
                perspectiveIds: action.payload
            }
        }
        case 'sortByAuthorLast': {
            return {
                ...state,
                sortBy: action.payload
            }
        }
        case 'sortByKeyword': {
            return {
                ...state,
                sortBy: action.payload.sortBy,
                sortByKeywordId: action.payload.sortByKeywordId,
                all: action.payload.all

            }
        }
        case 'SHOW_ALL_PERSPECTIVE': {
            return {
                ...state,
                all: action.payload
            }
        }
        case 'SET_PERSPECTIVE_LIKE': {
            let perspectives = state.perspectives;
            perspectives[action.payload.id].loves = action.payload.loves;
            perspectives[action.payload.id].isLike = action.payload.isLike;
            return {
                ...state,
                perspectives: perspectives
            }
        }
        case 'SET_IS_LIKE': {
            return {
                ...state,
                isLike: action.payload
            }
        }

        // case "GET_CONCEPT_DETAILS":
        //     // console.log("Dispatch Called!");
        //     return {
        //         ...state,
        //         conceptsWithDetails: [...action.payload]
        //     }
        // case "GET_CONCEPT_KEYWORDS":
        //     return {
        //         ...state,
        //         keywords: [...action.payload],
        //     }
        // case "GET_CONCEPT_PERSPECTIVEIDS":
        //     return {
        //         ...state,
        //         perspectiveids: [...action.payload]
        //     }
        // case "REFRESH_CONCEPT_PAGE":
        //     return {
        //         ...state,

        //     }
        // case 'FLUSH_CONCEPT':
        //     return {
        //         loading: true
        //     }
        case 'GET_LIST_RELATED':
            return {
                ...state,
                listRelatedData: action.payload,
            }
        // case 'LOADING_FALSE':
        //     console.log("Loading False")
        //     return {
        //         ...state,
        //         loading: false
        //     }
        // case 'LOADING_TRUE':
        //     console.log("Loaing True")
        //     return {
        //         loading: true,
        //     }
        default:
            return state;
    }
}