export function getAuthorCluster(id, via) {
    // console.log('Get Author Cluster action', id, via);
    return dispatch => {
        dispatch({
            type: "REQUEST_STARTED"
        })
        getAuthorClusterHelper(id, via).then(data => {
            let normalizedData=normalize(data,[authorClusterSchema]);
            normalizedData.data=data;
            console.log(normalizedData)
            dispatch({
                type: "GET_AUTHOR_CLUSTER_DETAILS",
                payload: normalizedData,
            })
        }).catch(error => {
            dispatch({
                type: "NETWORK_ERROR"
            })
            console.log('Error while getting detailed author cluster', error);
        })
    }
}
function getAuthorClusterHelper(id, via) {
    // alert(via)
    return new Promise(async (res, rej) => {
        let result = [];
        try {
            let result1 = {};
            if (via === 'Author Clusters') {
                //alert("getting data cluster");
                result1 = await axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/authorClusters/${id}/perspectives?relationships=true`);
            }
            else if (via === 'Author Groups') {
                result1 = await axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/authorGroups/${id}/perspectives?relationships=true`);
            }
            if (result1.data.responseType == 'success') {
                console.log("getting data response");
                result = result1.data.data;
            }
            res(result);
        } catch (e) {
            console.log('getting detailed author cluster error', e);
            rej(result);
        }
    })
}

export function setClickedState(obj){
    return dispatch=>{
        dispatch({
            type:'CLICKED_STATE',
            payload:obj
        })
    }
}

export function increasePerspectiveLike(id) {
    let like = {};
    like.type = "increase";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = true;
                dispatch({
                    type: 'AUTHOR_CLUSTER_PERSPECTIVE_LIKE',
                    payload: res.data.perspective
                })
            })
    }
}
export function decreasePerspectiveLike(id) {
    let like = {};
    like.type = "decrease";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = false;
                dispatch({
                    type: 'AUTHOR_CLUSTER_PERSPECTIVE_LIKE',
                    payload: res.data.perspective
                })
            })
    }
}