const initialState = {
    authorClustersWithDetails: [],
    authorClusterConceptsDiscrete: [],
    authorCluster: {},
    perspectives: [],
    authorClicked: false,
    conceptClicked: true,
    resort:3
}
export default (state = initialState, action = {}) => {
    switch (action.type) {
        case 'CLICKED_STATE': {
            return {
                ...state,
                authorClicked: action.payload.authorClicked,
                conceptClicked: action.payload.conceptClicked
            }
        }
        case 'AUTHOR_CLUSTER_PERSPECTIVE_LIKE': {
            let perspectives = state.authorCluster;
            perspectives[action.payload.id].loves = action.payload.loves;
            perspectives[action.payload.id].isLike = action.payload.isLike;
            return {
                ...state,
                authorCluster: perspectives
            }
        }
        case "GET_AUTHOR_CLUSTER_DETAILS":
            return {
                ...state,
                authorClustersWithDetails: [...action.payload.data],
                authorCluster: action.payload.entities.authorCluster,
                perspectives: [...action.payload.result]
            }
        case "GET_AUTHOR_CLUSTER_CONCEPTS_DISCRETE":
            return {
                ...state,
                authorClusterConceptsDiscrete: [...action.payload],
            }

        case 'FLUSH_AUTHOR_CLUSTER':
            return {

            }
        case "GET_CONCEPT_PERSPECTIVEIDS":
            return {
                ...state,
                perspectiveids: [...action.payload]
            }
        default:
            return state;
    }
}