import {schema} from 'normalizr';
const author=new schema.Entity("authors");
const keyword=new schema.Entity("keywords");
const perspective=new schema.Entity("perspectives",{
    Author:author,
    Keywords:[keyword]
});
const concept=new schema .Entity("concepts",{
    Perspectives:[perspective]
})
export const conceptClusterSchema=new schema.Entity("conceptCluster",{
    Concepts:[concept]
});