export function getAllAsync(name, data, callback) {
    if (name) {
        if (data.length > 0) {
            let results = [];
            for (let i = 0; i < data.length; i++) {
                if (data[i].label.toLowerCase().startsWith(name.toLowerCase())) {
                    if (results.length < 9) {
                        results.push(data[i]);
                    }
                    else {
                        break;
                    }
                }
            }
            callback(null, shuffle(results));
        }
        else {
            callback(null, []);
        }
    }
}

