export function login(account) {
    return (dispatch) => {
        let obj = {};

        obj.username = account.Email;
        obj.password = account.Password

        dispatch(loginRequest());

        //let accountJSON = JSON.stringify(obj);
        
        return axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/login`, obj, {
            headers: {
                'Content-Type': 'application/json;charset=UTF-8',
                'accept': 'application/json'
            }
        }).then((response) => {
            let res = JSON.parse(JSON.stringify(response));
            console.log(res)
            console.log(res.data.success)
            if (res.data.success) {
                dispatch(loginSuccess(res.data.user));

            } else {
                dispatch(loginFailed());
            }
        }).catch(e => {
            dispatch(loginFailed());
        })


    }
}

export function loginRequest() {
    return {
        type: 'LOGIN_REQUEST'
    }
}

export function loginSuccess(userInfo) {
    return {
        type: "LOGIN_SUCCESS",
        userInfo: userInfo
    }
}

export function loginFailed() {
    return {
        type: "LOGIN_FAILURE"
    }
}

//Logout 


export function logout() {
    return {
        type: "LOGOUT"
    }
}



export function signup(account) {
    return (dispatch) => {
        let obj = {};

        obj.username = account.Username;
        obj.email = account.Email;
        obj.password = account.Password

        let temp = {};
        temp["User"] = obj;



        dispatch(signupRequest());
        //console.log(accountJSON);
        //let accountJSON = temp;

        return axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/users`, temp, {
            headers: {
                'Content-Type': 'application/json;charset=UTF-8',
                'accept': 'application/json'
            }
        }).then((response) => {
            let res = JSON.parse(JSON.stringify(response));
            console.log(res)
            // console.log(res.data.responseType==='success')
            if (res.data.responseType === 'success') {
                dispatch(signupSuccess(res.data.data));
            } else {
                dispatch(signupFailed());
            }
        }).catch(e => {
            dispatch(signupFailed());
        })


    }
}

export function signupRequest() {
    return {
        type: 'SIGNUP_REQUEST'
    }
}


export function signupSuccess(userInfo) {
    return {
        type: "SIGNUP_SUCCESS",
        userInfo: userInfo
    }
}

export function signupFailed() {
    return {
        type: "SIGNUP_FAILURE"
    }
}
