import React, { Component } from 'react';
//import { ShareButton } from "react-custom-share";
import { FacebookProvider, Share } from 'react-facebook';

import {
    FacebookIcon,TwitterIcon
} from 'react-share';
import TwitterSharedButton from './TwitterSharedButton';
class ShareButtons extends Component {
    constructor(props) {
        super(props)
    }
    render() {
        const { id } = this.props;
        const link = `http://conceptionary-frontend.s3-website.us-east-2.amazonaws.com/perspective/${id}`
        return (
          <div>
          <FacebookProvider appId="930857800581355">
            <Share href={link}>
              {({ handleClick}) => (
               
             <button type="button" onClick={handleClick} style={{backgroundColor: 'transparent',border: 'none'}}> <FacebookIcon size={32} round={true} /></button>
              )}
            </Share>
          </FacebookProvider>
          <FacebookProvider appId="930857800581355">
            <Share href={link}>
              {({ handleClick}) => (
               
             <button type="button" onClick={handleClick} style={{backgroundColor: 'transparent',border: 'none'}}> <TwitterIcon size={32} round={true} /></button>
              )}
            </Share>
          </FacebookProvider>
     
          </div>
        );
      }
}

export default ShareButtons;
