import React, { Component } from 'react';
//import { ShareButton } from "react-custom-share";
import ShareLink from 'react-twitter-share-link'
import {Helmet} from "react-helmet";

import './TwitterShareButton.css'
import {
    TwitterShareButton,TwitterIcon
} from 'react-share';
class TwitterSharedButton extends Component {
    constructor(props) {
        super(props)
    }
   
    render() {
        const { id } = this.props;
        const links = `http://conceptionary-frontend.s3-website.us-east-2.amazonaws.com/perspective/${id}`
        return (
        //     <ShareLink link={links}>
        //         {link => (
        //         <a href={link} target='_blank'>Share this on Twitter</a>
        //         )}
        //  </ShareLink>
            <div>
            <Helmet>
                <meta name="twitter:card" content="summary_large_image" />
                <meta name="twitter:site" content="@nytimes" />
                <meta name="twitter:creator" content="@SarahMaslinNir" />
                <meta name="twitter:title" content="Parade of Fans for Houston’s Funeral" />
                <meta name="twitter:description" content="NEWARK - The guest list and parade of limousines with celebrities emerging from them seemed more suited to a red carpet event in Hollywood or New York than than a gritty stretch of Sussex Avenue near the former site of the James M. Baxter Terrace public housing project here." />
                <meta name="twitter:image" content="http://graphics8.nytimes.com/images/2012/02/19/us/19whitney-span/19whitney-span-articleLarge.jpg" />
            </Helmet>
        <TwitterShareButton
            url={links}
            title="Hello World. \n My name is Jennifer. \n What is your name?"
            className="Demo__some-network__share-button">
            <TwitterIcon
            size={32}
            round />
      </TwitterShareButton>
      </div>
        );
      }
}

export default TwitterSharedButton;
