import React, { Component } from 'react';
import "./TopNavBar.css";
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom'
import { bindActionCreators } from 'redux';
import "./ReactSelect.css";
import "react-virtualized/styles.css";
import "react-virtualized-select/styles.css";
import chroma from 'chroma-js'
import AsyncSelect from 'react-select/lib/Async';
import { setSelectedCategory, setSelectedItem, login, logout, signup, flushState, getAllAsync, setAutherClusterSelectedItems, prefetchConceptsForNavbar, prefetchConceptClustersForNavbar, prefetchAuthorsForNavbar, prefetchAuthorClustersForNavbar, prefetchAuthorGroupsForNavbar, getAuthorGroups, addTag, removeTag } from "../../actions/navbaractions";
import { Redirect } from 'react-router-dom';
import Modal from 'react-responsive-modal';
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import '../../../node_modules/bootstrap/dist/js/bootstrap';
import Loader from 'react-loader-spinner'
import Joi from 'joi-browser/dist/joi-browser'
import axios from 'axios';
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
} from 'reactstrap';

const server = process.env.REACT_APP_SERVER;
class TopNavBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTag: [],
            searchCategory: this.props.navbar.searchCategory,
            seletedSearch: "",
            selectedOption: {},
            authorClusterSelectedOptions: "",
            concepts: this.props.navbar.concepts,
            conceptClusters: this.props.navbar.conceptClusters,
            authors: this.props.navbar.authors,
            authorClusters: this.props.navbar.authorClusters,
            authorGroups: this.props.navbar.authorGroups,
            dropdownData: [],
            tags: this.props.navbar.tags,
            labelsArray: [],
            loadOptionsData: [],
            suggestions: [],
            modalOpen: false,
            modalSignupOpen: false,
            account: {
                Email: '',
                Password: '',
                Username: ''
            },
            errors: [],
            loginRequestStatus_admin: true,
            loginStatus_admin: true,
            userAccount: [],
            filter:false
        }

        // console.log("Top Navbar state in Constructor", this.props)
        this.handleChange = this.handleChange.bind(this);
        this.loadOptions = this.loadOptions.bind(this);
        this.renderRedirect = this.renderRedirect.bind(this);
        this.onSelectedCatagoryChange = this.onSelectedCatagoryChange.bind(this);
        //this.renderRedirectCategory = this.renderRedirectCategory.bind(this);
        this.searchAllConceptionary = this.searchAllConceptionary.bind(this);
    }
    schema = {
        Email: Joi.string()
            .required()
            .label("Email"),
        Password: Joi.string()
            .label("Password")
            .required(),
        Username: Joi.string().allow('', null)
    };


    componentDidMount() {
        if (this.props.loginStatus_admin) {
            this.props.history.push('/');
        }
    }
    shuffle(a) {
        // for (let i = a.length - 1; i > 0; i--) {
        //     const j = Math.floor(Math.random() * (i + 1));
        //     [a[i], a[j]] = [a[j], a[i]];
        // }
        return a;
    }

    toggleModalOpen = () => {
        this.setState(prevState => ({ modalOpen: !(prevState.modalOpen) }));
    }

    toggleModalSignupOpen = () => {
        this.setState(prevState => ({ modalSignupOpen: !(prevState.modalSignupOpen) }));
    }




    // Login Actions


    validateProperty(input) {
        const obj = { [input.name]: input.value };
        const schema = { [input.name]: this.schema[input.name] };
        const { error } = Joi.validate(obj, schema);
        if (!error) return null;
        return error.details[0].message;
    }

    /* *********************************************** HANDLE INPUT CHANGE EVENT *************************************** */

    validate = () => {
        const result = Joi.validate(this.state.account, this.schema, {
            abortEarly: false
        });
        if (!result.error) return null;

        const errors = [];

        for (let item of result.error.details) errors[item.path[0]] = item.message;
        return errors;
    };

    handleLoginChange = ({ currentTarget: input }) => {
        const errors = [...this.state.errors];

        const errorMessage = this.validateProperty(input);

        if (errorMessage)
            errors[input.name] = errorMessage;
        else
            delete errors[input.name];

        const account = { ...this.state.account };

        account[input.name] = input.value;
        this.setState({ account, errors });

    };


    handleSubmit = (e, isSignup) => {
        e.preventDefault();
        // this.setState({loginRequestStatus_admin:false})
        const errors = this.validate();

        if (!errors) {
            if (isSignup) this.props.signup(this.state.account)
            else this.props.login(this.state.account);
        }
        if (this.props.loginStatus_admin) {
            this.setState({ errors: errors || {} });
        }
    }

    // end login actions


    searchAllConceptionary(inputValue, callback) {

        axios.get(`${server}/api/rest/v1/home/all/` + inputValue).then(data => {
            const { responseType } = data.data;
            if (responseType === 'success') {
                const selectedOption = data.data.data;
                let suggestions = [...this.state.suggestions];

                selectedOption.forEach(x => {
                    let obj = {};
                    obj.id = x.id.toString();
                    obj.label = x.label;
                    suggestions.push(obj);
                })
                this.setState({ suggestions });


                callback(null, this.shuffle(selectedOption));
            }
            else {
                alert('error')
            }
        }).catch(error => {
            console.log('Error while prefetching author groups for navbar', error);
        });
    }


    searchAuthors(inputValue, callback) {
        axios.get(`${server}/api/rest/v1/authors/search/` + inputValue).then(data => {
            const { responseType } = data.data;
            if (responseType === 'success') {
                const selectedOption = data.data.data;
                this.setState({ selectedOption });

                callback(null, this.shuffle(selectedOption));
            }
            else {
                alert('error')
            }
        }).catch(error => {
            console.log('Error while prefetching authors for navbar', error);
        });
    }


    searchConcepts(inputValue, callback) {
        axios.get(`${server}/api/rest/v1/concepts/search/` + inputValue).then(data => {
            const { responseType } = data.data;
            if (responseType === 'success') {
                const selectedOption = data.data.data;
                this.setState({ selectedOption });

                callback(null, this.shuffle(selectedOption));
            }
            else {
                alert('error')
            }
        }).catch(error => {
            console.log('Error while prefetching concepts for navbar', error);
        });
    }


    searchConceptClusters(inputValue, callback) {

        axios.get(`${server}/api/rest/v1/conceptClusters/search/` + inputValue).then(data => {
            const { responseType } = data.data;
            if (responseType === 'success') {
                const selectedOption = data.data.data;
                // this.setState({selectedOption});

                callback(null, this.shuffle(selectedOption));
            }
            else {
                alert('error')
            }
        }).catch(error => {

            console.log('Error while prefetching concept clusters for navbar', error);
        });
    }


    searchAuthorClusters(inputValue, callback) {
        axios.get(`${server}/api/rest/v1/authorClusters/search/` + inputValue).then(data => {
            const { responseType } = data.data;
            if (responseType === 'success') {
                const selectedOption = data.data.data;
                this.setState({ selectedOption });
                callback(null, this.shuffle(selectedOption));
            }
            else {
                alert('error')
            }
        }).catch(error => {
            console.log('Error while prefetching author clusters for navbar', error);
        });
    }

    searchAuthorGroupsNav(inputValue, callback) {
        axios.get(`${server}/api/rest/v1/authorGroups/search/` + inputValue).then(data => {
            const { responseType } = data.data;
            if (responseType === 'success') {
                const selectedOption = data.data.data;
                this.setState({ selectedOption });
                callback(null, this.shuffle(selectedOption));
            }
            else {
                alert('error')
            }
        }).catch(error => {
            console.log('Error while prefetching author groups for navbar', error);
        });
    }

    searchAuthorGroups(labels, callback) {
        axios.post(`${server}/api/rest/v1/authors/search`, labels).then(data => {
            const { responseType } = data.data;
            if (responseType === 'success') {
                const selectedOption = data.data.data;
                callback(null, this.shuffle(selectedOption));
            }
            else {
                alert('error')
            }
        }).catch(error => {
            console.log('Error while prefetching author groups for navbar', error);
        });
    }


    onTagsChanged = (tag) => {
        // this.setState({filter:false})
        this.props.addTag(tag);
        if (this.state.tags.length > 0) {
            this.setState({
                searchCategory: 'AuthorGroups'
            });
            this.props.setSelectedCategory("AuthorGroups");
        }
        if (this.state.tags.length <= 0) {
            this.setState({
                searchCategory: this.props.navbar.searchCategory
            });
            this.props.setSelectedCategory("All");
        }
        

        this.setState({ suggestions: [],filter:!this.state.filter })

    };

    onInputChanged = (e) => {
        //console.log(`input value is now: ${e.target.value}`);
    }

    handleDelete = (i) => {
        const tags = this.state.tags.slice(0)
        tags.splice(i, 1)
        this.setState({ tags }, function () {
            if (this.state.tags.length > 0) {
                this.props.setSelectedCategory("AuthorGroups");
            }
            if (this.state.tags.length <= 0) {
                this.props.setSelectedCategory("All");
            }
        })
        this.setState({ suggestions: [] })


    }

    handleAddition = (tag) => {
        let tags = [...this.state.tags];
        tags.push({
            id: tag.id.toString(),
            label: tag.label,
            type: tag.type
        });
        this.setState({ tags }, function () {
            if (this.state.tags.length > 0) {
                this.props.setSelectedCategory("AuthorGroups");
            }
            if (this.state.tags.length <= 0) {
                this.props.setSelectedCategory("All");
            }
        });
        this.setState({ suggestions: [] })

        //console.log(this.state.tags);
    }
    loadTags = (input, cb) => {
        axios.post(`${server}/api/rest/v1/authorGroups/search`, input).then(data => {
            const { responseType } = data.data;
            if (responseType === 'success') {
                const selectedOption = data.data.data;
                let suggestions = [];
                selectedOption.forEach(x => {
                    let obj = {};
                    obj.id = x.id.toString();
                    obj.label = x.label;
                    obj.value = x.label;
                    obj.type = x.type;
                    suggestions.push(obj);
                })
                this.setState({ suggestions });
                cb(null, suggestions);
            }
            else {
                console.log("");
            }
        }).catch(error => {
            console.log('Error while prefetching suggestions for navbar', error);
        });
    }
    loadSuggestions = (label) => {
        let tagsObject = {};
        if (this.state.tags.length < 1) {
            tagsObject = {};
            tagsObject.type = "both";
            tagsObject.labels = label;
        } else {
            tagsObject = {};
            tagsObject.labels = label;
            tagsObject.type = "group";
            tagsObject.id = this.state.tags.map(x => x.id);

        }
        let query = {};
        query.tagsObject = tagsObject;
        return new Promise((resolve, reject) => {
            this.loadTags(query, function (err, suggestions) {

                resolve(suggestions);
            })
        })

    }

    handleChange = (selectedOption) => {
        if (selectedOption.label !== undefined) {
            this.props.setSelectedItem(selectedOption);
            this.setState({
                selectedOption
            });
        }
    }

    handleTagChange = (selectedTag) => {
        if (selectedTag.label !== undefined) {
            this.state.tags.push(selectedTag);
            this.setState({ tags: this.state.tags });
        }
    }

    onSelectedCatagoryChange(e) {
        if (this.props.navbar.tags.length > 0) {
            this.setState({
                searchCategory: "AuthorGroups",
                seletedSearch: e
            });
            this.props.flushState();
            this.props.setSelectedCategory("AuthorGroups");
        } else {
            this.setState({
                seletedSearch: e
            });
            this.props.flushState();
            this.props.setSelectedCategory(e);
        }
    }
    loadOptions(inputValue) {
        if (this.state.searchCategory === "Concept") {

            return new Promise(resolve => {
                this.searchConcepts(inputValue, function (err, results) {
                    resolve(results);
                })
            })
        }
        else if (this.state.searchCategory === "Concept Clusters") {

            return new Promise(resolve => {
                this.searchConceptClusters(inputValue, function (err, results) {
                    resolve(results);
                })
            })
        }

        else if (this.state.searchCategory === "AuthorGroups") {
            let obj = {};
            obj.labels = this.state.tags;
            obj.category = this.state.seletedSearch;
            obj.label = inputValue;

            return new Promise(resolve => {
                this.searchAuthorGroups(obj, function (err, results) {
                    resolve(results);
                })
            })
        }

        else if (this.state.searchCategory === "Author") {
            return new Promise(resolve => {
                this.searchAuthors(inputValue, function (err, results) {
                    resolve(results);
                })
            })
        }
        else if (this.state.searchCategory === "Author Clusters") {
            return new Promise(resolve => {
                this.searchAuthorClusters(inputValue, function (err, results) {
                    resolve(results);
                })
            })
        }
        else if (this.state.searchCategory === "Author Groups") {
            return new Promise(resolve => {
                this.searchAuthorGroupsNav(inputValue, function (err, results) {
                    resolve(results);
                })
            })
        }
        else if (this.state.searchCategory === "All") {

            return new Promise(resolve => {
                this.searchAllConceptionary(inputValue, function (err, results) {

                    resolve(results);
                })
            })
        }
    };
    renderRedirect = () => {
        if (this.state.selectedOption) {
            if (this.state.selectedOption.category) {
                switch (this.state.selectedOption.category) {
                    case "Concepts":
                        return <Redirect to='/concepts' />
                    case "Concept-Clusters":
                        return <Redirect to='/concept-clusters' />
                    case "Authors":
                        return <Redirect to='/authors' />
                    case "Author-Clusters":
                        return <Redirect to='/author-clusters' />
                    case "Author-Groups":
                        return <Redirect to='/author-groups' />
                    default:
                        return null
                }
            }
        }
    }
    removeTag = id => {
        this.props.removeTag(id);
    }
    componentWillReceiveProps(nextProps) {
        if (this.state.selectedOption !== nextProps.navbar.selectedOption && nextProps.navbar.selectedOption) {
            this.setState({ selectedOption: nextProps.navbar.selectedOption });
        }

        // if ( this.props.loginStatus_admin !=nextProps.loginStatus_admin  && nextProps.loginStatus_admin) {

        // }
        if (nextProps.navbar.SIGNUP_SUCCESS) {
            let account = {
                Email: '',
                Password: '',
                Username: ''
            };
            this.setState({
                account

            });
        }
        if (this.props.loginStatus_admin != nextProps.loginStatus_admin && !nextProps.invalidLogin && nextProps.loginStatus_admin) {
            this.setState({ modalOpen: false })
        }


        if (this.props.navbar.tags.length !== nextProps.navbar.tags.length) {

            if (nextProps.navbar.tags.length > 0) {
                this.props.setSelectedCategory("AuthorGroups");
            }
            if (nextProps.navbar.tags.length <= 0) {
                this.props.setSelectedCategory(this.state.seletedSearch ? this.state.seletedSearch : 'All');
            }
        }

        this.setState({
            searchCategory: nextProps.navbar.searchCategory,
            concepts: nextProps.navbar.concepts,
            conceptClusters: nextProps.navbar.conceptClusters,
            authors: nextProps.navbar.authors,
            authorClusters: nextProps.navbar.authorClusters,
            authorGroups: nextProps.navbar.authorGroups,
            selectedOption: nextProps.navbar.selectedOption,
            tags: nextProps.navbar.tags

        });
        if (nextProps.navbar.selectedOption.label !== undefined) {
            if (nextProps.navbar.tags.length > 0) {
                let filters = nextProps.navbar.tags.map(x => x.label);
                let name = nextProps.navbar.selectedOption.value;
                let category = nextProps.navbar.selectedOption.category;
                let url = `/${category}/${name}?`;
                for (let i = 0; i < filters.length; i++) {
                    if (i == 0) {
                        url = url.concat(`filters=${filters[i]}`);
                    } else {
                        url = url.concat(`&filters=${filters[i]}`);
                    }

                }
                window.history.pushState(null, null, url);
            } else {
                let name = nextProps.navbar.selectedOption.value;
                let category = nextProps.navbar.selectedOption.category;
                let url = `/${category}/${name}`;
                  window.history.pushState(null, null, url);
                //this.props.history.push(url);
            }
        }
    }
    render() {
        return (
            <div>
                {this.renderRedirect()}
                <Navbar style={{ backgroundColor: [getBackgroudColor(this.state.selectedOption.page)],padding:"0px",paddingLeft:"20px",paddingRight:"20px" }} light expand="md">
                    <NavbarBrand href="/">Conceptionary</NavbarBrand>
                    <NavbarToggler onClick={this.toggle} />
                    <Collapse isOpen={this.state.isOpen} navbar>
                        <Nav className="navbar">
                            <form className="form-inline">
                                <span style={{ display: "inline-block" }}>
                                    <select style={{marginTop:"-15px", height:37,padding: "5px 2px", borderRadius: "5px", border: "1px solid white" }} onChange={(e) => this.onSelectedCatagoryChange(e.target.value)} value={this.state.seletedSearch}>
                                        <option selected={this.state.searchCategory === "All"} value="All">All</option>
                                        <option selected={this.state.searchCategory === "Concept"} value="Concept">Concepts</option>
                                        <option selected={this.state.searchCategory === "Author"} value="Author">Authors</option>
                                        <option selected={this.state.searchCategory === "Concept Clusters"} value="Concept Clusters">Concept Clusters</option>
                                        <option selected={this.state.searchCategory === "Author Groups"} value="Author Groups">Author Groups</option>
                                    </select>
                                </span>
                                <span style={{ display: "inline-block",padding:"5px 3px",marginTop:"-10px" }}>
                                    <AsyncSelect className="ml-2 width-300"
                                        value={this.state.selectedOption}
                                        loadOptions={this.loadOptions}
                                        onChange={this.handleChange}
                                        onFocus={()=> this.setState({selectedOption:""})}
                                        styles={colourStyles}
                                    />

                                </span>
                               
                               
                                
                            </form>
                            &nbsp;
                            &nbsp;
                        </Nav>
                        <Nav className="ml-auto" navbar>
                             
                                
                        <span style={{ display: "inline-block",paddingTop:"5px",paddingRight:'5px' }}>
                                    <ul>
                                        {this.state.tags.map(tag => {
                                            return <li style={{ display: 'inline-block', background: 'purple', color: "#fff", borderRadius: '8px', padding: '5px', fontSize: '13px', marginLeft: '2px' }}>{tag.label}
                                                <span style={{ marginLeft: '5px' }} className="badge" id="tag" onClick={() => { this.removeTag(tag.id) }}>X</span>
                                            </li>
                                        })}
                                    </ul>
                                </span>
                             <span className="ml-auto" style={{ display:this.state.filter?"inline-block":"none" }}>
                                    <AsyncSelect className="width-200"
                                        value={this.state.selectedTag}
                                        loadOptions={this.loadSuggestions}
                                        onChange={this.onTagsChanged}
                                        // styles={colourStyles}
                                        isDisabled={this.state.tags.length >= 2 ? true : false || this.state.searchCategory === "Author Groups"}
                                    />

                                </span>
                        <span style={{ display: "inline-block" }}>
                                        <NavItem  className="ml-auto" style={{margin:0,padding:0,cursor:'pointer'}} onClick={() => {this.setState({filter:!this.state.filter})}}><i id="filter" className="fa fa-filter filter"></i></NavItem>
                                </span>

                            {
                                this.props.loginStatus_admin ?
                                    <React.Fragment>
                                        <span style={{ padding: '0 5px' }}>
                                            <NavItem style={{ cursor: 'pointer' }}>
                                                {this.props.loggedInUserAccount_admin.username}

                                            </NavItem>
                                        </span>
                                        <span style={{ padding: '0 5px' }}>
                                            <NavItem style={{ cursor: 'pointer' }}>
                                                <span onClick={() => { this.props.logout() }}>Logout</span>
                                            </NavItem>
                                        </span>
                                        <span style={{ padding: '0 5px' }}>

                                            <button
                                                style={
                                                    {
                                                        border: '1px solid',
                                                        marginRight: '5px',
                                                        borderRadius: '50%',
                                                        color: 'black',
                                                        fontWeight: 'bold',
                                                        background: 'white'
                                                    }
                                                }
                                            >+</button>
                                        </span>

                                    </React.Fragment>
                                    :

                                    <span>
                                    <NavItem style={{ cursor: 'pointer' }}>
                                        <NavLink onClick={this.toggleModalOpen} >Log-in</NavLink>
                                    </NavItem>
                                    </span>
                                    

                            }
                        </Nav>
                    </Collapse>
                </Navbar>

                <Modal open={this.state.modalOpen} onClose={this.toggleModalOpen} center>
                    <h4 style={{ textAlign: "center" }}>Log in to your account</h4>

                    <div style={{ padding: '50px' }}>
                        <Form style={{ margin: 'auto', width: '300px', padding: '20px 30px', background: '#fff', boxShadow: '0 0 20px #ccc' }}>
                            <FormGroup>
                                {/* <Label style={{fontWeight:"normal",fontSize:'12px'}} className="text-muted" for="Email">Email</Label> */}
                                <Input type="text" name="Email" id="Email" onChange={(e) => this.handleLoginChange(e)} placeholder="username" />
                                {this.state.errors.Email && (
                                    <p style={{ padding: '3px', fontSize: '12px' }} className="alert alert-danger">{this.state.errors.Email}</p>
                                )}
                            </FormGroup>

                            <FormGroup>
                                {/* <Label style={{fontWeight:"normal",fontSize:'12px'}} className="text-muted" for="Password">Password</Label> */}
                                <Input type="Password" name="Password" id="Password" onChange={(e) => this.handleLoginChange(e)} placeholder="Password" />
                                {this.state.errors.Password && (
                                    <p style={{ padding: '3px', fontSize: '12px' }} className="alert alert-danger">{this.state.errors.Password}</p>
                                )}
                            </FormGroup>
                            {
                                this.props.invalidLogin ? <p className="alert alert-info">Invalid username/password</p> : null
                            }

                            <p style={{ color: '#641E89', textDecoration: 'underline', cursor: 'pointer' }}
                                onClick={() => {
                                    this.toggleModalOpen();
                                    this.toggleModalSignupOpen();
                                }}
                            >Don't have an account? signup</p>
                            <Button onClick={(e) => this.handleSubmit(e)}
                                style={{
                                    width: "100%",
                                    height: "40px",
                                    backgroundColor: "#641E89",
                                    color: "#fff",
                                    border: "1px solid white"
                                }} className="btn form-control">{this.state.loginRequestStatus_admin ? "Login" :
                                    <Loader
                                        type="Puff"
                                        color="#FFF"
                                        height="30"
                                        width="30"
                                    />}
                            </Button>

                        </Form>
                    </div>
                </Modal>

                <Modal open={this.state.modalSignupOpen} onClose={this.toggleModalSignupOpen} center>
                    <h4 style={{ textAlign: "center" }}>Sign up for a new account</h4>
                    {
                        this.props.navbar.SIGNUP_SUCCESS &&

                        (
                            <div class="alert alert-success alert-dismissible">
                                <span href="#" class="close" data-dismiss="alert" aria-label="close">&times;</span>
                                <strong>Success!</strong> user created please login.
                        </div>
                        )
                    }
                    <div style={{ padding: '50px' }}>
                        <Form style={{ margin: 'auto', width: '300px', padding: '20px 30px', background: '#fff', boxShadow: '0 0 20px #ccc' }}>
                            <FormGroup>
                                {/* <Label style={{fontWeight:"normal",fontSize:'12px'}} className="text-muted" for="Email">Email</Label> */}
                                <Input type="text" name="Email" id="Email" value={this.state.account.Email} onChange={(e) => this.handleLoginChange(e)} placeholder="Email" />
                                {this.state.errors.Email && (
                                    <p style={{ padding: '3px', fontSize: '12px' }} className="alert alert-danger">{this.state.errors.Email}</p>
                                )}
                            </FormGroup>

                            <FormGroup>
                                {/* <Label style={{fontWeight:"normal",fontSize:'12px'}} className="text-muted" for="Password">Password</Label> */}
                                <Input type="Username" name="Username" id="Username" value={this.state.account.Username} onChange={(e) => this.handleLoginChange(e)} placeholder="Username" />
                                {this.state.errors.Username && (
                                    <p style={{ padding: '3px', fontSize: '12px' }} className="alert alert-danger">{this.state.errors.Username}</p>
                                )}
                            </FormGroup>
                            <FormGroup>
                                {/* <Label style={{fontWeight:"normal",fontSize:'12px'}} className="text-muted" for="Password">Password</Label> */}
                                <Input type="Password" name="Password" id="Password" value={this.state.account.Password} onChange={(e) => this.handleLoginChange(e)} placeholder="Password" />
                                {this.state.errors.Password && (
                                    <p style={{ padding: '3px', fontSize: '12px' }} className="alert alert-danger">{this.state.errors.Password}</p>
                                )}
                            </FormGroup>

                            <p style={{ color: '#641E89', textDecoration: 'underline', cursor: 'pointer' }}
                                onClick={() => {
                                    this.toggleModalSignupOpen();
                                    this.toggleModalOpen();
                                }}
                            >Already have an account? login</p>
                            {/* <p style={{color:'#641E89',textDecoration:'underline', cursor:'pointer'}}>Forgot Password ?</p> */}
                            <Button onClick={(e) => this.handleSubmit(e, true)}
                                style={{
                                    width: "100%",
                                    height: "40px",
                                    backgroundColor: "#641E89",
                                    color: "#fff",
                                    border: "1px solid white"
                                }} className="btn form-control">{this.state.loginRequestStatus_admin ? "Signup" :
                                    <Loader
                                        type="Puff"
                                        color="#FFF"
                                        height="30"
                                        width="30"
                                    />}
                            </Button>

                        </Form>
                    </div>
                </Modal>

            </div>
        );
    }
}

const dot = (color = '#ccc') => ({
    alignItems: 'center',
    display: 'flex',

    ':before': {
        backgroundColor: color,
        borderRadius: 10,
        content: '" "',
        display: 'block',
        marginRight: 8,
        height: 10,
        width: 10,
    },
});
const colourStyles = {
    control: styles => ({ ...styles, backgroundColor: 'white' }),
    option: (styles, { data, isDisabled, isFocused, isSelected }) => {
        const color = chroma(data.color);
        return {
            ...styles,
            backgroundColor: isDisabled
                ? null
                : isSelected ? data.color : isFocused ? color.alpha(0.1).css() : null,
            color: isDisabled
                ? '#ccc'
                : isSelected
                    ? chroma.contrast(color, 'white') > 2 ? 'white' : 'black'
                    : data.color,
            cursor: isDisabled ? 'not-allowed' : 'default',
        };
    },
    input: styles => ({ ...styles, ...dot() }),
    placeholder: styles => ({ ...styles, ...dot() }),
    singleValue: (styles, { data }) => ({ ...styles, ...dot(data.color) }),
};
const getBackgroudColor = (page) => {
    switch (page) {
        case 'Concepts':
            return "#696969";
        case 'Concept Clusters':
            return "#CD5C5C";
        case 'Authors':
            return "#A52A2A";
        case 'Author Clusters':
            return "#800080";
        default:
            return "#a4b2b7";
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        setSelectedCategory: bindActionCreators(setSelectedCategory, dispatch),
        setSelectedItem: bindActionCreators(setSelectedItem, dispatch),
        flushState: bindActionCreators(flushState, dispatch),
        setAutherClusterSelectedItems: bindActionCreators(setAutherClusterSelectedItems, dispatch),
        getAuthorGroups: bindActionCreators(getAuthorGroups, dispatch),
        login: bindActionCreators(login, dispatch),
        signup: bindActionCreators(signup, dispatch),
        logout: bindActionCreators(logout, dispatch),
        addTag: bindActionCreators(addTag, dispatch),
        removeTag: bindActionCreators(removeTag, dispatch)

    }
}
const mapStateToProps = (state) => {
    return {
        navbar: state.navbar,
        userExist_admin: state.navbar.userExist_admin,
        loginRequestStatus_admin: state.navbar.loginRequestStatus_admin,
        loginStatus_admin: state.navbar.loginStatus_admin,
        invalidLogin: state.navbar.invalidLogin,
        loggedInUserAccount_admin: state.navbar.loggedInUserAccount_admin
    }
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TopNavBar));